import axios from 'axios';

const addCSRFTag = adapter => config => {
  const CSRFTag = document.querySelector('meta[name="csrf-token"]');
  if (CSRFTag === null) {
    throw new Error('CSRFTag is null, request would fail');
  }

  const token = CSRFTag.getAttribute('content');

  const newConfig = config;
  newConfig.headers['X-CSRF-TOKEN'] = token;
  return adapter(newConfig);
};

const clientFactory = adapter => {
  return axios.create({
    adapter: addCSRFTag(adapter),
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
    },
  });
};

const errorHandler = error => {
  const handlingError = { ...error };
  const retryCount = error.config.params.retries;

  if (
    (error.response.status === 408 || error.response.status === 504) &&
    retryCount > 1
  ) {
    handlingError.config.params.retries = retryCount - 1;
    return Promise.resolve(this.axiosWithRetries(handlingError.config));
  }

  return Promise.reject(new Error({ ...handlingError }));
};

const successHandler = response => response;

const clientWithRetriesFactory = httpClient => {
  httpClient.interceptors.response.use(
    response => successHandler(response),
    error => errorHandler(error)
  );
  return httpClient;
};

const client = clientFactory(axios.defaults.adapter);

const axiosWithRetries = clientWithRetriesFactory(
  clientFactory(axios.defaults.adapter)
);

export default client;
export {
  clientFactory,
  clientWithRetriesFactory,
  axiosWithRetries,
  errorHandler,
  successHandler,
};
