/* eslint-disable class-methods-use-this */
import TriggerAsyncRequest from '../shared/utils/trigger-async-request';

const httpClient = new TriggerAsyncRequest();

const embeddablesPath = window && window.APIURL ? window.APIURL : '/formularios';

export class EmbeddablesRepository {
  static HTTP_OK = 200;

  constructor(apiClient, endpoint) {
    this.endpoint = endpoint;
    this.api = apiClient;
  }

  get(id) {
    return this.api.getJSON(`${this.endpoint}/${id}.json`);
  }

  getTemplates() {
    return this.api.getJSON(`${this.endpoint}/templates.json`);
  }

  createEmbeddable(info) {
    return this.api.post(this.endpoint, info);
  }

  sendEmail(info) {
    return this.api.post('/enviar-email', info);
  }

  updateEmbeddable(id, data) {
    return this.api.patch(`${this.endpoint}/${id}.json`, data);
  }

  updateContent(embeddableId, contentId, payload) {
    return this.api.put(`${this.endpoint}/${embeddableId}/contents/${contentId}`, { payload });
  }

  publishEmbeddable(embeddableId) {
    return this.api.patch(`${this.endpoint}/${embeddableId}/publicar`);
  }

  previewEmbeddable(payload) {
    return this.api.post('/content/payload/preview.html', payload);
  }

  updateTitle = (embeddableId, payload) => {
    return this.api.patch(`${this.endpoint}/${embeddableId}`, payload);
  }

  duplicate = (embeddableId, payload) => {
    return this.api.post(`${this.endpoint}/${embeddableId}/duplicar-novo-titulo`, payload);
  }
}

export default new EmbeddablesRepository(httpClient, embeddablesPath);
