import React from 'react';
import PropTypes from 'prop-types';
import {
  Tooltip,
  OverlayTrigger,
} from 'react-bootstrap';
import I18n from '../../../i18n';

const t = (key, params) => I18n.t(`double_opt_in.components.configuration_section.${key}`, params);

const DoubleOptInHelpTooltip = ({ formBaseType }) => {
  const message = t(`${formBaseType}.page_display_name_tooltip`);
  const tooltip = (
    <Tooltip placement="right" className="in" id="tooltip-right">
      {message}
    </Tooltip>
  );

  return (
    <OverlayTrigger placement="right" overlay={tooltip}>
      <i className="xicon-help page-display-name-help-icon" />
    </OverlayTrigger>
  );
};

DoubleOptInHelpTooltip.propTypes = {
  formBaseType: PropTypes.string.isRequired,
};

export default DoubleOptInHelpTooltip;
