import React from 'react';
import PropTypes from 'prop-types';

const NoneFormActionInput = ({ isActive, inputId, inputName }) => {
  return (
    <div className="">
      <input
        disabled={!isActive}
        type="hidden"
        defaultValue=""
        name={inputName}
        id={inputId}
      />
    </div>
  );
};

NoneFormActionInput.propTypes = {
  isActive: PropTypes.bool,
  inputName: PropTypes.string.isRequired,
  inputId: PropTypes.string,
};

NoneFormActionInput.defaultProps = {
  inputId: '',
  isActive: false,
};

export default NoneFormActionInput;
