import React from 'react';
import PropTypes from 'prop-types';
import times from 'lodash/times';

/**
 * It is used to fill spaces while another component is loading.
 */

const UIContentLoadingLine = ({ size, rows }) => {
    return (
      <React.Fragment>
        { times(rows, (i) => { return (<div key={i} className={`content-loading-line ${size}`} />) } ) }
      </React.Fragment>
    );
};

UIContentLoadingLine.defaultProps = {
    rows: 1,
    size: '',
};

UIContentLoadingLine.propTypes = {
    /** Number of rows for the loading component. */
    rows: PropTypes.number,
    /** Size of the component. It can be regular (default) or slim. */
    size: PropTypes.oneOf(['slim', '']),
};

export default UIContentLoadingLine;
