import React, { Component } from 'react';
import PropTypes from 'prop-types';
import PreviewModal from '../shared/components/lead-gen/common/PreviewModal';

export class Preview extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: {
        title: '', feature: '', show: false, content: '',
      },
    };
  }

  componentDidMount() {
    const {
      title, payload, feature, previewEmbeddable,
    } = this.props;
    previewEmbeddable({ payload }).then((response) => {
      this.setState({
        data: {
          title,
          feature,
          show: true,
          content: response.data,
        },
      });
    });
  }

  render() {
    const { data } = this.state;
    const { onHide } = this.props;
    return <PreviewModal {...data} onHide={onHide} />;
  }
}

Preview.propTypes = {
  title: PropTypes.string.isRequired,
  payload: PropTypes.objectOf(PropTypes.any).isRequired,
  feature: PropTypes.string,
  onHide: PropTypes.func,
  previewEmbeddable: PropTypes.func.isRequired,
};

Preview.defaultProps = {
  feature: '',
  onHide: () => {},
};

export default Preview;
