import React, { Component } from 'react';
import PropTypes from 'prop-types';
import cs from 'classnames';
import I18n from '../../../shared/i18n';

const t = (key, params) => I18n.t(`lead_gen.embeddable_forms.conversion_tab.notification.${key}`, params);

class EmailNotification extends Component {
  renderInput() {
    const {
      getFieldProps,
      getFieldError,
      update,
      notifyConversionEmail,
    } = this.props;
    return (
      <div id="notification-input-container" className="form-group">
        <input
          className="form-control"
          type="email"
          id="notification-email"
          {...getFieldProps('notifyConversionEmail', {
            onChange: e => update('landingPageActions', 'notifyConversionEmail', e.target.value),
            initialValue: notifyConversionEmail,
            rules: [
              {
                type: 'email',
                required: true,
                message: t('error.email'),
              },
            ],
          })}
        />
        {getFieldError('notifyConversionEmail')}
      </div>
    );
  }

  render() {
    const { getFieldError, notifyConversionEmailEnabled, update } = this.props;

    return (
      <div id="notification-row" className="row">
        <div className="col-sm-3">
          <div>
            <h5>{t('title')}</h5>
          </div>
        </div>
        <div className="col-sm-8">
          <div
            className={cs('checkbox', {
              'has-error': getFieldError('notifyConversionEmail'),
            })}
          >
            <label htmlFor="notification-enabled">
              <input type="checkbox" id="notification-enabled" onChange={e => update('landingPageActions', 'notifyConversionEmailEnabled', e.target.checked)} checked={notifyConversionEmailEnabled} />
              {t('text')}
              <small className="help-block">{t('description')}</small>
              {notifyConversionEmailEnabled ? (
                this.renderInput()
              ) : null }
            </label>
          </div>
        </div>
      </div>
    );
  }
}

EmailNotification.propTypes = {
  notifyConversionEmailEnabled: PropTypes.bool,
  notifyConversionEmail: PropTypes.string,
  getFieldProps: PropTypes.func,
  getFieldError: PropTypes.func,
  update: PropTypes.func,
};

EmailNotification.defaultProps = {
  notifyConversionEmailEnabled: false,
  notifyConversionEmail: '',
  getFieldError: () => {},
  getFieldProps: () => {},
  update: () => {},
};

export default EmailNotification;
