import * as actions from './actions';

const initialState = {
  loading: true,
  embeddable: null,
  savingAndRedirecting: false,
};

export const embeddableApp = (state = initialState, action) => {
  switch (action.type) {
    case actions.SET_DIRTY_MODAL_FOR_UNSAVED_CHANGES:
      return {
        ...state,
        onSaveForCurrentStepMethod: action.onSaveForCurrentStepMethod,
        checkForUnsavedChangesAtCurrentStepMethod: action.checkForUnsavedChangesAtCurrentStepMethod,
      };
    case actions.CLOSE_DIRTY_MODAL:
      return {
        ...state,
        displayDirtyModal: false,
      };
    case actions.SHOW_DIRTY_MODAL:
      return {
        ...state,
        callbackForOriginalActionMethod: action.callbackForOriginalActionMethod,
        displayDirtyModal: true,
      };
    case actions.CLEAN_DIRTY_MODAL_CONFIG:
      return {
        ...state,
        onSaveForCurrentStepMethod: undefined,
        checkForUnsavedChangesAtCurrentStepMethod: undefined,
        displayDirtyModal: false,
      };
    case actions.LOAD_EMBEDDABLE_REQUEST:
      return {
        ...state,
        loading: true,
      };
    case actions.LOAD_EMBEDDABLE_FAILURE:
      return {
        ...state,
        loading: false,
        error: action.error,
      };
    case actions.LOAD_EMBEDDABLE_SUCCESS:
      return {
        ...state,
        loading: false,
        embeddable: action.embeddable,
      };
    case actions.UPDATE_EMBEDDABLE_REQUEST:
      return {
        ...state,
        updating: true,
        embeddable: action.embeddableInfo,
      };
    case actions.UPDATE_EMBEDDABLE_FAILURE:
      return {
        ...state,
        error: action.error,
        updating: false,
      };
    case actions.UPDATE_EMBEDDABLE_SUCCESS:
      return {
        ...state,
        updating: false,
        embeddable: {
          ...action.embeddableInfo,
          payload: action.embeddableInfo.payload,
        },
      };
    case actions.UPDATE_CONFIGURATION_REQUEST:
      return {
        ...state,
        updating: true,
      };
    case actions.UPDATE_CONFIGURATION_FAILURE:
      return {
        ...state,
        error: action.error,
        updating: false,
      };
    case actions.UPDATE_CONFIGURATION_SUCCESS:
      return {
        ...state,
        updating: false,
        embeddable: {
          ...action.embeddable,
          actions: {
            ...action.embeddable.actions,
            landing_page_action: action.landingPageAction,
          },
        },
      };
    default:
      return state;
  }
};
