import React from 'react';
import PropTypes from 'prop-types';
import { createForm, formShape } from 'rc-form';
import FormGroup from '../../../shared/components/lead-gen/common/FormGroup';
import I18n from '../../../shared/i18n';

const t = (key, params) => I18n.t(`lead_gen.embeddable_forms.templates.modal.form.${key}`, params);

export class Form extends React.Component {
  render() {
    const { getFieldProps, getFieldError } = this.props.form;
    const { submitEmbeddable } = this.props;


    return (
      <form onSubmit={(e) => {
        e.preventDefault();
        submitEmbeddable();
        e.stopPropagation();
      }}
      >
        <FormGroup
          label={t('name')}
          help={t('name_help')}
          errors={getFieldError('landing_page_embeddable.title')}
        >
          <input
            className="form-control"
            {...getFieldProps('landing_page_embeddable.title', {
              initialValue: '',
              rules: [
                {
                  required: true,
                  message: t('error.empty'),
                },
              ],
            })}
          />
          <input
            type="hidden"
            {...getFieldProps('landing_page_embeddable.landing_page_template_id', {
              initialValue: this.props.activeTemplateId,
            })}
          />
        </FormGroup>
      </form>
    );
  }
}

Form.propTypes = {
  form: formShape.isRequired,
  submitEmbeddable: PropTypes.func,
  activeTemplateId: PropTypes.string,
};

Form.defaultProps = {
  activeTemplateId: '',
  submitEmbeddable: () => {},
};

export default createForm()(Form);
