/* eslint-disable react/no-danger */
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import cs from 'classnames';
import CenteredSpinner from '../common/CenteredSpinner';
import { embeddableType } from '../../shapes';
import EmbeddableCode from './EmbeddableCode';
import SendCode from './SendCode';
import Identifier from './Identifier';

class MainContent extends Component {
  render() {
    const {
      loading,
      embeddable,
      toggleShowModal,
      codeSnippet,
    } = this.props;

    return (
      <section className={cs({ 'loading-overlay': loading })}>
        {loading ? (
          <CenteredSpinner />
        ) : (
          <React.Fragment>
            <div className="row">
              <EmbeddableCode
                embeddableId={embeddable.id}
                urlSlug={embeddable.url_slug}
                analyticsUAID={embeddable.analytics_ua_id}
                codeSnippet={codeSnippet}
              />
              <hr className="spacer-md invisible" />
              <SendCode toggleShowModal={toggleShowModal} />
              <hr className="spacer-xs invisible" />
            </div>
            <hr className="spacer-md" />
            <div className="row">
              <Identifier identifier={embeddable.identifier} />
            </div>
          </React.Fragment>
        )}
      </section>
    );
  }
}

MainContent.propTypes = {
  embeddable: embeddableType,
  loading: PropTypes.bool,
  toggleShowModal: PropTypes.func,
  codeSnippet: PropTypes.string,
};

MainContent.defaultProps = {
  embeddable: null,
  loading: false,
  toggleShowModal: () => {},
  codeSnippet: '',
};

export default MainContent;
