import PropTypes from 'prop-types';
import React from 'react';
import I18n from '../../../shared/i18n';

const t = (key, params) => I18n.t(`lead_gen.embeddable_forms.template_card.${key}`, params);

const Author = ({ name, country }) => {
  if (!name) return <br />;
  return (
    <span>
      {t('by')} <strong>{name}</strong> ({I18n.t(`countries.${country}`)})
    </span>
  );
};

Author.propTypes = {
  name: PropTypes.string,
  country: PropTypes.string,
};

Author.defaultProps = {
  name: null,
  country: null,
};

export default Author;
