import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import * as actions from '../../../actions';
import { embeddableType } from '../../../shapes';
import BrowserConfirmationBeforeUnload from '../../../shared/utils/browser-confirmation-before-unload';
import Modal from '../Modal/index';
import RDSLogo from './RDSLogo';
import I18n from '../../../shared/i18n';

const t = (key, params) => I18n.t(`lead_gen.embeddable_forms.${key}`, params);

export class NavBar extends React.PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      showModal: false,
    };
  }

  checkForUnsavedChanges(event) {
    const { checkForUnsavedChangesAtCurrentStepMethod, showDirtyModal } = this.props;

    if (checkForUnsavedChangesAtCurrentStepMethod !== undefined) {
      if (checkForUnsavedChangesAtCurrentStepMethod()) {
        event.preventDefault();
        const elementHref = event.currentTarget.href;
        const callback = () => {
          BrowserConfirmationBeforeUnload.removeConfirmation();
          window.location = elementHref;
        };
        showDirtyModal(callback);
        return false;
      }
    }
    return true;
  }

  renderLink(title) {
    return (
      <a href={window.Routes.landing_page_embeddables_path()} className="header-navbar-link" onClick={e => this.checkForUnsavedChanges(e)}>
        {title}
      </a>
    );
  }

  renderBreadCrumbsActions(title, embeddableTitle) {
    return (
      <div className="navbar-header-breadcrumb">
        {this.renderLink(t('header.title'))}
        <button onClick={() => this.setState({ showModal: true })} className="btn btn-link header-navbar-link" type="button">
          {title || embeddableTitle}
        </button>
      </div>
    );
  }

  render() {
    const { showModal } = this.state;
    const { title, embeddableTitle, embeddable } = this.props;

    return (
      <div>
        <nav className="builder-header" role="navigation">
          <div className="navbar-container">
            <div className="navbar-header">
              <a className="navbar-brand logo " href="/home" title="Incio" id="link-title-react" onClick={e => this.checkForUnsavedChanges(e)}>
                <RDSLogo />
              </a>
            </div>
            {this.renderBreadCrumbsActions(title, embeddableTitle)}
            <div className="navbar-header-actions navbar-header-actions-right">
              {this.renderLink(t('header.exit'))}
            </div>
          </div>
        </nav>
        { embeddable && (
          <Modal
            initialTitle={embeddable.title}
            embeddableId={embeddable.id}
            show={showModal}
            onHide={() => this.setState({ showModal: false })}
          />
        )}
      </div>
    );
  }
}

NavBar.propTypes = {
  checkForUnsavedChangesAtCurrentStepMethod: PropTypes.func,
  showDirtyModal: PropTypes.func.isRequired,
  embeddable: embeddableType,
  title: PropTypes.string,
  embeddableTitle: PropTypes.string,
};

NavBar.defaultProps = {
  checkForUnsavedChangesAtCurrentStepMethod: undefined,
  embeddable: null,
  title: '',
  embeddableTitle: '',
};

const mapStateToProps = (state) => {
  return {
    checkForUnsavedChangesAtCurrentStepMethod: state.embeddableApp.checkForUnsavedChangesAtCurrentStepMethod,
    embeddableTitle: state.embeddableApp.embeddable ? state.embeddableApp.embeddable.title : '',
    embeddable: state.embeddableApp.embeddable,
    title: state.updateTitleReducer ? state.updateTitleReducer.title : '',
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    showDirtyModal: (callbackForOriginalActionMethod) => {
      dispatch(actions.showDirtyModal(callbackForOriginalActionMethod));
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(NavBar);
