import React from 'react';
import cs from 'classnames';
import PropTypes from 'prop-types';

import Step from './Step';

/**
 * Visual representation of the steps necessary to finish a task. Usually, each step represents
 * a screen that the user must perform an action toward a goal.
 */

const UISteps = props => {
  const { collapseOn, className, children, ...rest } = props;

  const collapseClass = collapseOn != null ? `collapse-on-${collapseOn}` : '';

  const csResult = cs(
    'tangram steps',
    className,
    collapseClass
  );

  return <ul {...rest} className={csResult}>{children}</ul>;
};

UISteps.Step = Step;

UISteps.defaultProps = {
  collapseOn: null,
};

UISteps.propTypes = {
  /** Additional classes. */
  className: PropTypes.string,
  /** Collapses the steps to a smaller form when the screen reaches the given breakpoint in pixels. */
  collapseOn: PropTypes.oneOf([1000, 900, 800, 700, 600]),
  children: PropTypes.node,
};

export default UISteps;
