import React from 'react';
import elementType from 'prop-types-extra/lib/elementType';
import cs from 'classnames';
import PropTypes from 'prop-types';

const UIModalTitle = props => {
  const { as: Component, className, ...rest } = props;

  const classes = cs('title', className);

  return <Component {...rest} className={classes} />;
};

UIModalTitle.propTypes = {
  /** Custom DOM element */
  as: elementType,
  /** Additional classes */
  className: PropTypes.string,
};

UIModalTitle.defaultProps = {
  as: 'h4',
};

export default UIModalTitle;
