import React from 'react';
import PropTypes from 'prop-types';
import { Collapse } from 'react-bootstrap';

export const SelectBoxContent = ({ children }) => {
  return (
    <div className="select-box-content">
      { children }
    </div>
  );
};

export const SelectBoxPane = ({ children, description, htmlID, open }) => {
  return (
    <div className="select-box-pane" id={htmlID}>
      <Collapse in={open} >
        <div>
          <small className="help-block">{description}</small>
          { children }
        </div>
      </Collapse>
    </div>
  );
};

SelectBoxPane.propTypes = {
  description: PropTypes.string,
  htmlID: PropTypes.string.isRequired,
  open: PropTypes.bool,
  children: PropTypes.instanceOf(Object),
};

SelectBoxPane.defaultProps = {
  open: true,
  description: '',
  children: {},
};

SelectBoxContent.propTypes = {
  children: PropTypes.oneOfType([
    PropTypes.instanceOf(Array),
    PropTypes.instanceOf(Object),
  ]),
};

SelectBoxContent.defaultProps = {
  children: [],
};
