import React from 'react';
import PropTypes from 'prop-types';
import { Route, Switch } from 'react-router-dom';
import Editor from '../Editor';
import Configuration from '../Configuration';
import Code from '../Code';
import * as actions from '../../actions';
import { Preview } from '../Preview';
import I18n from '../../shared/i18n';

const t = (key, params) => I18n.t(`lead_gen.embeddable_forms.${key}`, params);

class EditRoutes extends React.Component {
  static preview(embeddable) {
    return actions.previewEmbeddable(embeddable);
  }

  constructor(props) {
    super(props);
    this.state = {
      embeddable: null,
    };
    this.handlePreview = this.onPreview.bind(this);
  }

  onPreview(embeddable) {
    this.setState({ embeddable });
  }

  hidePreviewPayload() {
    this.setState({ embeddable: null });
  }

  renderPreview() {
    const { embeddable } = this.state;
    if (embeddable) {
      const previewProps = {
        previewEmbeddable: EditRoutes.preview.bind(this),
        title: embeddable.title,
        payload: embeddable.payload,
        feature: t('header.title'),
        onHide: this.hidePreviewPayload.bind(this),
      };
      return (<Preview {...previewProps} />);
    }
    return null;
  }

  renderRoute(path, Component) {
    const { embeddableId } = this.props;
    return (
      <Route
        path={path}
        render={(props) => {
          return (
            <Component
              embeddableId={embeddableId}
              onPreview={this.handlePreview}
              {...props}
              {...this.props}
            />
          );
        }}
      />
    );
  }

  render() {
    return (
      <div>
        <Switch>
          {this.renderRoute('/:embeddableId/editar/aparencia', Editor)}
          {this.renderRoute('/:embeddableId/editar/conversao', Configuration)}
          {this.renderRoute('/:embeddableId/editar/codigo', Code)}
        </Switch>
        {this.renderPreview()}
      </div>
    );
  }
}

EditRoutes.propTypes = {
  embeddableId: PropTypes.string.isRequired,
};

export default EditRoutes;
