import RedirectFormActionInput from './RedirectFormActionInput';
import DownloadFormActionInput from './DownloadFormActionInput';
import NoneFormActionInput from './NoneFormActionInput';

export { RedirectFormActionInput, NoneFormActionInput, DownloadFormActionInput };


export const createFormActionConfig = (id, title, description, value, imageUrl, inputComponent) => {
  return {
    title,
    description,
    htmlID: id,
    value,
    inputName: `${id}`,
    imageUrl,
    inputId: `${id}-input-id`,
    selectItemHtmlName: `${id}-html-name`,
    active: false,
    inputComponent,
  };
};
