import RDSMAxios from './RDSMAxiosClient.js';
import axios from 'axios';

class TriggerAsyncRequest {
  constructor(customHeaders) {
    const config = RDSMAxios.defaults;
    const headers = {...RDSMAxios.defaults.headers, 'X-Requested-With': 'XMLHttpRequest' }
    config.headers = headers;
    this.client = axios.create(config);
  }

  getJSON(requestRoute) {
    return this.client.get(requestRoute)
      .then(response => response);
  }

  get(requestRoute) {
    return this.client.get(requestRoute)
      .then(response => response);
  }

  put(requestRoute, data) {
    return this.client.put(requestRoute, data)
      .then(response => response);
  }

  patch(requestRoute, data) {
    return this.client.patch(requestRoute, data)
      .then(response => response);
  }

  post(requestRoute, data) {
    return this.client.post(requestRoute, data)
      .then(response => response);
  }

  delete(requestRoute) {
    return this.client.delete(requestRoute)
      .then(response => response);
  }

  addHeader(name, value) {
    this.client.defaults.headers.common[name] = value;
  }
}

export default TriggerAsyncRequest;
