import validateEmailFormat from '../email-validator';
import validateFormatAndDomain from './validateFormatAndDomain';

export default function validateSenderEmail(email) {
  return new Promise((resolve) => {
    const hasEmailFormatError = !validateEmailFormat(email);

    if (hasEmailFormatError) {
      return resolve({ valid: false, error: 'invalid_format' });
    }

    return validateFormatAndDomain(email).then(({ valid, error }) => resolve({ valid, error }));
  });
}
