import React from 'react';
import PropTypes from 'prop-types';


export class SelectBoxItem extends React.Component {
  constructor(props) {
    super(props);
    this.setItemRef = (element) => {
      this.itemRef = element;
    };
  }

  render() {
    return (
      <div className="select-box-item">
        <input
          onClick={() => this.props.onClick(this.itemRef)}
          type={this.props.type}
          className={this.props.className}
          name={this.props.name}
          ref={this.setItemRef}
          defaultChecked={this.props.selected}
          value={this.props.value}
          data-target={this.props.dataTarget}
        />
        <label htmlFor={this.props.dataTarget} className="label-container">
          <span className="title">{this.props.title}</span>
          { this.props.imageUrl ? (<img
            src={this.props.imageUrl}
            alt={this.props.imageAlt}
          />) : false }
        </label>
      </div>
    );
  }
}

SelectBoxItem.propTypes = {
  value: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  type: PropTypes.string,
  title: PropTypes.string,
  dataTarget: PropTypes.string,
  imageUrl: PropTypes.string,
  imageAlt: PropTypes.string,
  selected: PropTypes.bool,
  onClick: PropTypes.func,
  className: PropTypes.string,
};

SelectBoxItem.defaultProps = {
  onClick: () => {},
  type: 'radio',
  title: '',
  dataTarget: '',
  imageUrl: '',
  imageAlt: '',
  selected: false,
  className: 'select-box-item-radio',
};

export default SelectBoxItem;
