import React from 'react';
import SafeAnchor from 'react-bootstrap/lib/SafeAnchor';
import PropTypes from 'prop-types';
import { elementType } from 'prop-types-extra';
import cs from 'classnames';

/** Button styled component. */

class UIButton extends React.Component {
  renderAnchor({ as, ...props }, className) {
    return (
      <SafeAnchor
        {...props}
        className={cs(className, props.disabled && 'disabled')}
      />
    );
  }

  renderButton({ as, ...props }, className) {
    const Component = as || 'button';
    return (
      <Component
        {...props}
        type={props.type || 'button'}
        className={className}
      />
    );
  }

  render() {
    const {
      active,
      fluid,
      priority,
      use,
      size,
      loading,
      className,
      ...props
    } = this.props;

    const csResult = cs(
      className,
      'tangram button',
      priority,
      { [size]: size },
      { [use]: use },
      { active },
      { fluid },
      { loading },
    );

    if (props.href) {
      return this.renderAnchor(props, csResult);
    }

    return this.renderButton(props, csResult);
  }
}

UIButton.defaultProps = {
  active: false,
  fluid: false,
  disabled: false,
  priority: 'secondary',
  loading: false,
};

UIButton.propTypes = {
  /** Button priority. */
  priority: PropTypes.oneOf(['primary', 'secondary', 'tertiary']),
  /** Size of the button. */
  size: PropTypes.oneOf(['slim']),
  /** Color style related with it's use. */
  use: PropTypes.oneOf(['danger']),
  /** Additional classes to be passed to the element. */
  className: PropTypes.string,
  active: PropTypes.bool,
  disabled: PropTypes.bool,
  /** Takes the full width of the container element */
  fluid: PropTypes.bool,
  /** Render a loading spinner instead of inner html */
  loading: PropTypes.bool,
  /** Click event callback. */
  onClick: PropTypes.func,
  /** You can priority a custom element type for this component. */
  as: elementType,
  /** Makes the button a link with a `href` attribute. */
  href: PropTypes.string,
  /** Defines HTML button type attribute. */
  type: PropTypes.oneOf(['button', 'reset', 'submit']),
};

export default UIButton;
