import React, { Component } from 'react';
import PropTypes from 'prop-types';
import cs from 'classnames';
import I18n from '../../../shared/i18n';

const t = (key, params) => I18n.t(`lead_gen.embeddable_forms.conversion_tab.acknowledgment.${key}`, params);

class FeedbackMessage extends Component {
  renderInput() {
    const {
      getFieldProps,
      getFieldError,
      feedbackAlert,
      update,
    } = this.props;
    return (
      <div id="feedback-input-container" className="form-group">
        <input
          className="form-control"
          type="text"
          id="feedback-message"
          {...getFieldProps('feedbackAlert', {
            initialValue: feedbackAlert,
            onChange: e => update('landingPageActions', 'feedbackAlert', e.target.value),
            rules: [
              {
                required: true,
                message: t('error.empty'),
              },
            ],
          })}
        />
        {getFieldError('feedbackAlert')}
      </div>
    );
  }

  render() {
    const {
      getFieldError,
      feedbackAlertEnabled,
      update,
    } = this.props;
    return (
      <div id="acknowledgment-row" className="row">
        <div className="col-sm-3">
          <div>
            <h5>{t('title')}</h5>
          </div>
        </div>
        <div className="col-sm-8">
          <div
            className={cs('checkbox', { 'has-error': getFieldError('feedbackAlert') })}
          >
            {feedbackAlertEnabled}
            <label htmlFor="message-active">
              <input type="checkbox" id="feedback-enabled" onChange={e => update('landingPageActions', 'feedbackAlertEnabled', e.target.checked)} checked={feedbackAlertEnabled} />
              {t('message.text')}
              <small className="help-block">{t('message.description')}</small>
              {feedbackAlertEnabled ? (
                this.renderInput()
              ) : (null)}
            </label>
          </div>
        </div>
      </div>
    );
  }
}

FeedbackMessage.propTypes = {
  feedbackAlertEnabled: PropTypes.bool,
  feedbackAlert: PropTypes.string,
  getFieldProps: PropTypes.func,
  getFieldError: PropTypes.func,
  update: PropTypes.func,
};

FeedbackMessage.defaultProps = {
  feedbackAlertEnabled: false,
  feedbackAlert: '',
  getFieldError: () => {},
  getFieldProps: () => {},
  update: () => {},
};

export default FeedbackMessage;
