import React from 'react';
import { Route, Switch } from 'react-router-dom';
import Templates from '../Templates';
import EditRoutes from './EditRoutes';
import WarningForUnsavedChanges from '../common/WarningForUnsavedChanges';

const EmbeddableRoutes = () => {
  return (
    <Switch>
      <Route path="/novo" render={props => <Templates {...props} />} />
      <Route
        path="/:embeddableId"
        render={(renderProps) => {
          const { embeddableId } = renderProps.match.params;
          return (
            <React.Fragment>
              <EditRoutes {...renderProps} embeddableId={embeddableId} />
              <WarningForUnsavedChanges />
            </React.Fragment>
          );
        }}
      />
    </Switch>
  );
};

export default EmbeddableRoutes;
