import React, { Component } from 'react';
import PropTypes from 'prop-types';
import I18n from '../../shared/i18n';

const t = (key, params) => I18n.t(`lead_gen.embeddable_forms.code.${key}`, params);
class SendCode extends Component {
  render() {
    const { toggleShowModal } = this.props;
    return (
      <React.Fragment>
        <div className="col-sm-3" />
        <div className="col-sm-9">
          <h5><strong>{t('forward.section_title')}</strong></h5>
          <p className="help-block small">{t('forward.help')}</p>
          <button
            className="btn btn-default btn-sm open-forward-modal"
            type="button"
            name="button"
            onClick={() => toggleShowModal()}
          >
            {t('forward.button')}
          </button>
        </div>
      </React.Fragment>
    );
  }
}

SendCode.propTypes = {
  toggleShowModal: PropTypes.func.isRequired,
};

export default SendCode;
