class CopyToClipboard {
  constructor(triggerElement, contentElement, successCallback) {
    this.triggerElement = triggerElement;
    this.contentElement = contentElement;
    this.successCallback = successCallback;

    this.copyToClipboard = this.copyToClipboard.bind(this);
  }

  bind() {
    this.triggerElement.addEventListener('click', this.copyToClipboard);
  }

  copyToClipboard() {
    this.clearSelection();

    this.selectScriptRange();
    const successful = document.execCommand('copy');

    if (successful) {
      if (this.successCallback) this.successCallback();
    }

    this.clearSelection();
  }

  // eslint-disable-next-line class-methods-use-this
  clearSelection() {
    document.getSelection().removeAllRanges();
  }

  selectScriptRange() {
    const range = document.createRange();
    range.selectNode(this.contentElement);
    window.getSelection().addRange(range);
  }
}

export default CopyToClipboard;
