class I18n {
  constructor(translation){
    this.locale = window && window.I18n && window.I18n.locale ? window.I18n.locale : 'en';
    this.locale = Object.keys(translation).includes(this.locale) ? this.locale : 'en';

    this.translation = translation;
  }

  t(key) {
    return this.translation[this.locale][key];
  }
}

export default I18n;
