const getFormAction = (configuration) => {
  let formAction;
  let redirectURL;
  let downloadURL;
  if (configuration.redirect_to_active) {
    if (configuration.redirect_to.match('s3.amazonaws')) {
      formAction = 'download';
      downloadURL = configuration.redirect_to;
    } else {
      formAction = 'redirect';
      redirectURL = configuration.redirect_to;
    }
  } else {
    formAction = 'none';
  }
  return { formAction, downloadURL, redirectURL };
};

export const feedbackEmailToState = (feedbackEmail) => {
  return {
    active: feedbackEmail.lead_notification_active,
    name: feedbackEmail.from_name,
    email: feedbackEmail.from_email,
    subject: feedbackEmail.subject,
    body: feedbackEmail.body,
  };
};

export const toState = (embeddable) => {
  const configuration = { ...embeddable.actions.landing_page_action };
  const doubleOptIn = { ...embeddable.double_opt_in_configuration };
  const notifyConversionEmailEnabled = configuration.notify_conversion_email_active || false;
  const notifyConversionEmail = configuration.notify_conversion_email || '';
  const feedbackAlertEnabled = configuration.feedback_alert_active || false;
  const feedbackAlert = configuration.feedback_alert || '';
  const captchaStatus = embeddable.captcha_status;
  let feedbackEmail = { };
  if (embeddable.actions.landing_page_action.landing_page_lead_email_attributes) {
    feedbackEmail = feedbackEmailToState(embeddable
      .actions
      .landing_page_action
      .landing_page_lead_email_attributes);
  } else {
    feedbackEmail = { ...embeddable.feedback_email_configuration };
  }
  const { formAction, downloadURL, redirectURL } = getFormAction(configuration);
  return {
    landingPageActions: {
      notifyConversionEmailEnabled,
      notifyConversionEmail,
      feedbackAlertEnabled,
      feedbackAlert,
      formAction,
      redirectURL,
      downloadURL,
      redirectToEnabled: configuration.redirect_to_active,
    },
    doubleOptIn,
    feedbackEmail,
    captchaStatus,
  };
};

function getRedirectData(configuration) {
  const redirectToActive = ['redirect', 'download'].includes(configuration.formAction);
  let redirectTo = '';
  if (configuration.formAction === 'redirect') {
    redirectTo = configuration.redirectURL;
  } else if (configuration.formAction === 'download') {
    redirectTo = configuration.downloadURL;
  }
  return { redirectToActive, redirectTo };
}

function getConversionOptionsData(configuration) {
  const feedbackAlertData = { feedback_alert_active: configuration.feedbackAlertEnabled };
  const notifyConversionEmailData = {
    notify_conversion_email_active: configuration.notifyConversionEmailEnabled,
  };
  if (configuration.feedbackAlertEnabled) {
    feedbackAlertData.feedback_alert = configuration.feedbackAlert;
  }
  if (configuration.notifyConversionEmailEnabled) {
    notifyConversionEmailData.notify_conversion_email = configuration.notifyConversionEmail;
  }
  return { feedbackAlertData, notifyConversionEmailData };
}

export const feedbackEmailFromState = (feedbackEmail) => {
  return {
    lead_notification_active: feedbackEmail.active,
    from_name: feedbackEmail.name,
    from_email: feedbackEmail.email,
    subject: feedbackEmail.subject,
    body: feedbackEmail.body,
  };
};

export const fromState = (landingPageActions, doubleOptIn, feedbackEmail, captchaStatus) => {
  const {
    feedbackAlertData,
    notifyConversionEmailData,
  } = getConversionOptionsData(landingPageActions);

  const { redirectToActive, redirectTo } = getRedirectData(landingPageActions);
  return {
    double_opt_in_configuration: {
      name: doubleOptIn.name,
      email: doubleOptIn.email,
      language: doubleOptIn.language,
      active: doubleOptIn.active,
      formBaseType: doubleOptIn.formBaseType,
      page_display_name: doubleOptIn.pageDisplayName,
    },
    landing_page_embeddable: {
      captcha_status: captchaStatus,
      landing_page_action_attributes: {
        redirect_to_active: redirectToActive,
        redirect_to: redirectTo,
        ...feedbackAlertData,
        ...notifyConversionEmailData,
        landing_page_lead_email_attributes: feedbackEmailFromState(feedbackEmail),
      },
    },
  };
};
