import React, { Component } from 'react';
import ReactQuill, { Quill } from 'react-quill';
import { formShape } from 'rc-form';
import PropTypes from 'prop-types';
import { setNativeValue } from '../../common/utils';
import I18n from '../../../shared/i18n';

const FontFamily = Quill.import('attributors/style/font');
const Font = Quill.import('formats/font');

const t = (key, params) => I18n.t(`lead_gen.embeddable_forms.conversion_tab.rich_editor.${key}`, params);

class RichEditor extends Component {
  getI18nCss(value, label) {
    return `.ql-snow .ql-picker.ql-size .ql-picker-label[data-value=${value}]::before, .ql-snow .ql-picker.ql-size .ql-picker-item[data-value=${value}]::before {
            content: '${label}';
          }`;
  }

  getBaseI18nCss(label) {
    return `.ql-snow .ql-picker.ql-size .ql-picker-label::before, .ql-snow .ql-picker.ql-size .ql-picker-item::before {
            content: '${label}';
          }`;
  }

  render() {
    const { content, form } = this.props;

    FontFamily.whitelist = RichEditor.fonts;
    Font.whitelist = RichEditor.fonts;
    Quill.register(Font, true);
    Quill.register(FontFamily, true);
    return (
      <div>
        <ReactQuill
          value={content}
          modules={RichEditor.modules}
          formats={RichEditor.formats}
          onChange={(html) => {
            const elem = form.getFieldInstance('feedbackEmail.body');
            if (elem) {
              setNativeValue(elem, html);
              const event = new Event('input', { bubbles: true });
              elem.dispatchEvent(event);
            }
          }}
          theme="snow"
        />
        <style>
          {this.getI18nCss('small', t('font_size.small'))}
          {this.getBaseI18nCss(t('font_size.normal'))}
          {this.getI18nCss('large', t('font_size.large'))}
          {this.getI18nCss('huge', t('font_size.huge'))}
        </style>
      </div>
    );
  }
}


RichEditor.fonts = [
  'Andale Mono', 'Arial Black', 'Arial',
  'Book Antiqua', 'Comic Sans MS', 'Courier New',
  'Droid Sans', 'Droid Serif', 'Georgia',
  'Helvetica', 'Impact', 'Lato', 'Lobster',
  'Open Sans', 'Oswald', 'PT Sans', 'Raleway',
  'Roboto', 'Source Sans Pro', 'Symbol',
  'Tahoma', 'Terminal', 'Times New Roman',
  'Trebuchet MS', 'Ubuntu', 'Verdana',
  'Webdings', 'Wingdings',
];

RichEditor.modules = {
  toolbar: [
    [{ header: 1 }, { header: 2 }, { font: RichEditor.fonts }],
    [{ size: [] }],
    ['bold', 'italic', 'underline', 'strike', 'blockquote'],
    [{ list: 'ordered' }, { list: 'bullet' },
      { indent: '-1' }, { indent: '+1' }],
    ['link'],
    ['clean'],
  ],
  clipboard: {
    matchVisual: false,
  },
};

RichEditor.formats = [
  'header', 'font', 'size',
  'bold', 'italic', 'underline', 'strike', 'blockquote',
  'list', 'bullet', 'indent',
  'link',
];


RichEditor.propTypes = {
  content: PropTypes.string,
  form: formShape.isRequired,
};

RichEditor.defaultProps = {
  content: '',
};

export default RichEditor;
