/* eslint-disable react/no-find-dom-node,no-return-assign */
import React from 'react';
import ReactDOM from 'react-dom';
import PropTypes from 'prop-types';
import { Button } from 'react-bootstrap';

import LoadingSpinner from '../rds-loading-spinner';

class LoadingButton extends React.Component {
  constructor(props) {
    super(props);
    this.dimensions = { width: null, height: null };
  }

  componentDidMount() {
    // It makes the button hold the same dimensions when text is replaced by the loading spinner
    const elem = ReactDOM.findDOMNode(this.elem);
    if (elem instanceof Element) {
      const { height, width } = elem.getBoundingClientRect();
      this.dimensions = { height, width };
    }
  }

  render() {
    const { children, isLoading, onClick, ...rest } = this.props;
    const { width, height } = this.dimensions;
    return (
      <Button
        {...rest}
        disabled={this.props.disabled || isLoading}
        onClick={!isLoading ? onClick : null}
        style={width && height ? { width, height } : null}
        ref={elem => this.elem = elem}
      >
        {isLoading ? <LoadingSpinner width={22} /> : children}
      </Button>
    );
  }
}

LoadingButton.defaultProps = {
  disabled: false,
  children: '',
  className: '',
  id: '',
  isLoading: false,
  bsStyle: 'default',
};

LoadingButton.propTypes = {
  className: PropTypes.string,
  id: PropTypes.string,
  isLoading: PropTypes.bool,
  onClick: PropTypes.func.isRequired,
  disabled: PropTypes.bool,
  children: PropTypes.node,
  bsStyle: PropTypes.string,
};

export default LoadingButton;
