import React from 'react';
import PropTypes from 'prop-types';
import { createForm, formShape } from 'rc-form';
import FromField from './FromField';
import ToField from './ToField';
import SubjectField from './SubjectField';
import BodyField from './BodyField';

export class Form extends React.Component {
  render() {
    const {
      defaultEmailBody,
      form,
    } = this.props;
    const { getFieldProps, getFieldError } = form;

    return (
      <form>
        <FromField getFieldProps={getFieldProps} getFieldError={getFieldError} />
        <ToField getFieldProps={getFieldProps} getFieldError={getFieldError} />
        <SubjectField getFieldProps={getFieldProps} getFieldError={getFieldError} />
        <BodyField
          getFieldProps={getFieldProps}
          getFieldError={getFieldError}
          defaultValue={defaultEmailBody}
        />
      </form>
    );
  }
}

Form.propTypes = {
  form: formShape.isRequired,
  defaultEmailBody: PropTypes.string,
};

Form.defaultProps = {
  defaultEmailBody: '',
};

export default createForm()(Form);
