/* eslint-disable jsx-a11y/no-noninteractive-tabindex,
                  jsx-a11y/no-static-element-interactions,
                  react/forbid-prop-types */
import React from 'react';
import cs from 'classnames';
import PropTypes from 'prop-types';

import SafeAnchor from 'react-bootstrap/lib/SafeAnchor';

const hasChildren = children => React.Children.count(children) > 0;

const Step = props => {
  const {
    className,
    active,
    disabled,
    href,
    label,
    children,
    clickable,
    onLabelClick,
    onLabelKeyDown,
    ...rest
  } = props;

  const csResult = cs('step', className, { active }, { disabled });

  const content =
    href && label ? (
      <SafeAnchor href={href} onClick={onLabelClick} onKeyDown={onLabelKeyDown}>
        {label}
      </SafeAnchor>
    ) : (
      <span
        className={cs({ clickable })}
        onClick={onLabelClick}
        onKeyDown={onLabelKeyDown}
        role={(onLabelClick || onLabelKeyDown) && 'button'}
        tabIndex={(onLabelClick || onLabelKeyDown) && 0}
      >
        {label}
      </span>
    );

  return (
    <li {...rest} className={csResult}>
      {hasChildren(children) ? children : content}
    </li>
  );
};

Step.propTypes = {
  /** Additional classes. */
  className: PropTypes.string,
  /** Apply styles indicating that this is the current active step. */
  active: PropTypes.bool,
  /** Apply styles indicating that this step is disabled. */
  disabled: PropTypes.bool,
  /** Apply clickable anchor styles to the label. To be used with onClick event */
  clickable: PropTypes.bool,
  /** Makes the step clickable by adding a anchor tag. */
  href: PropTypes.string,
  /** The text displayed inside the step. */
  label: PropTypes.string,
  /** Mouse click event for the step label */
  onLabelClick: PropTypes.func,
  /** Keyboard key down event for the step label */
  onLabelKeyDown: PropTypes.func,
  /** Custom children. */
  children: PropTypes.any,
};

Step.defaultProps = {
  className: '',
  active: false,
  disabled: false,
  clickable: false,
  href: '',
  label: '',
  children: [],
};

export default Step;
