import React from 'react';
import PropTypes from 'prop-types';
import cs from 'classnames';

const Box = props => {
  const {
    className,
    boxClassName,
    style,
    boxStyle,
    children,
    size,
    ...rest
  } = props;

  const styles = { display: 'block', ...style };
  const classes = cs('tangram', className);

  const boxClasses = cs('modal-dialog', 'box', size, boxClassName);

  return (
    <section
      tabIndex="-1"
      role="dialog"
      style={styles}
      className={classes}
      {...rest}
    >
      <div className={boxClasses} style={boxStyle} role="document">
        <div className="content">{children}</div>
      </div>
    </section>
  );
};

Box.propTypes = {
  /** Additional classes to be applied to the external element */
  className: PropTypes.string,
  /** Additional styles to be applied to the external element */
  style: PropTypes.object,
  /**
   * A css class to apply to the box DOM node.
   */
  boxClassName: PropTypes.string,
  /**
   * Styles to apply to the box DOM node.
   */
  boxStyle: PropTypes.string,
  /** Size of the modal */
  size: PropTypes.oneOf(['large']),
};

export default Box;
