import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { formShape } from 'rc-form';
import cs from 'classnames';
import RichEditor from './RichEditor';
import I18n from '../../../shared/i18n';

const t = (key, params) => I18n.t(`lead_gen.embeddable_forms.conversion_tab.acknowledgment.${key}`, params);

class FeedbackEmail extends Component {
  hiddenBodyInput() {
    const {
      getFieldProps,
      body,
      update,
    } = this.props;
    return (
      <input
        style={{ display: 'none' }}
        id="feedback-email-body"
        className="form-control"
        type="text"
        {...getFieldProps('feedbackEmail.body', {
          initialValue: body,
          onChange: (e) => {
            update('feedbackEmail', 'body', e.target.value);
          },
          rules: [
            {
              required: true,
              type: 'string',
              message: t('error.empty'),
              transform: value => value.replace(/^<p><br><\/p>$/, ''),
            },
          ],
        })}
      />
    );
  }

  renderInput(initialValue, field, tag = 'input') {
    const { getFieldProps, getFieldError, update } = this.props;
    const error = getFieldError(`feedbackEmail.${field}`);
    const DynamicInput = `${tag}`;
    return (
      <div className={cs('form-group field', { 'has-error': error })}>
        <label htmlFor={`feedback-email-${field}`}>{t(`email.${field}`)}</label>
        <DynamicInput
          id={`feedback-email-${field}`}
          className="form-control"
          type="text"
          {...getFieldProps(`feedbackEmail.${field}`, {
            initialValue,
            onChange: e => update('feedbackEmail', field, e.target.value),
            rules: [{ required: true, message: t('error.empty') }],
          })}
        />
        <span className="control-label">{error}</span>
      </div>
    );
  }

  renderEmailOptions() {
    const {
      name,
      email,
      subject,
      body,
      getFieldError,
      form,
    } = this.props;
    const error = getFieldError('feedbackEmail.body');
    return (
      <div id="feedback-email-fields">
        {this.renderInput(name, 'name')}
        {this.renderInput(email, 'email')}
        {this.renderInput(subject, 'subject')}
        <div className={cs('form-group field', { 'has-error': error })}>
          <label htmlFor="feedback-email-body">{t('email.body')}</label>
          <RichEditor content={body} form={form} />
          {this.hiddenBodyInput()}
          <div className="control-label">{error}</div>
        </div>
      </div>
    );
  }

  render() {
    const {
      active,
      update,
    } = this.props;
    return (
      <div className="row">
        <div className="col-sm-3" />
        <div className="col-sm-8">
          <div className="checkbox">
            <label htmlFor="feedback-email-active">
              <input type="checkbox" id="feedback-email-active" onChange={e => update('feedbackEmail', 'active', e.target.checked)} checked={active} />
              {t('email.text')}
              <small className="help-block">{t('email.description')}</small>
            </label>
          </div>
          {active ? (this.renderEmailOptions()) : (null)}
        </div>
      </div>
    );
  }
}

FeedbackEmail.propTypes = {
  getFieldProps: PropTypes.func,
  getFieldError: PropTypes.func,
  update: PropTypes.func,
  active: PropTypes.bool,
  name: PropTypes.string,
  email: PropTypes.string,
  subject: PropTypes.string,
  body: PropTypes.string,
  form: formShape.isRequired,
};

FeedbackEmail.defaultProps = {
  getFieldError: () => {},
  getFieldProps: () => {},
  update: () => {},
  active: false,
  name: '',
  email: '',
  subject: '',
  body: '',
};

export default FeedbackEmail;
