import React from 'react';
import PropTypes from 'prop-types';
import splitComponentProps from 'react-bootstrap/lib/utils/splitComponentProps';

import UIDialog from '../UIDialog/UIDialog';
import UIButton from '../UIButton/UIButton';

/**
 It is used to eliminate data whose action is irreversible.

 ***Context of application**:
 - Delete critical item from an edit
 - Delete item in listings
 - Delete items in bulk
 */

class UIDestructionDialog extends React.Component {
  static i18nKeys = [
    'title',
    'primaryLabel',
    'secondaryLabel',
    'tertiaryLabel',
    'message',
  ];

  static defaultIcon = (
    <svg width="40px" height="40px">
      <svg
        width="38px"
        height="38px"
        x="1"
        y="1"
        viewBox="0 0 42 42"
        xmlns="http://www.w3.org/2000/svg"
        xmlnsXlink="http://www.w3.org/1999/xlink"
      >
        <title>Destruction Icon</title>
        <g>
          <path
            d="M22.3207173,0.585786438 L41.2272211,19.4922902 C42.0082696,20.2733388 42.0082696,21.5396687 41.2272211,22.3207173 L22.3207173,41.2272211 C21.5396687,42.0082696 20.2733388,42.0082696 19.4922902,41.2272211 L0.585786438,22.3207173 C-0.195262146,21.5396687 -0.195262146,20.2733388 0.585786438,19.4922902 L19.4922902,0.585786438 C20.2733388,-0.195262146 21.5396687,-0.195262146 22.3207173,0.585786438 Z"
            fill="#FB5B41"
          />
          <path
            d="M22.5039062,23.4492188 L19.6445312,23.4492188 L19.046875,12 L23.1015625,12 L22.5039062,23.4492188 Z M19,27.4570312 C19,26.800778 19.1757795,26.3046892 19.5273438,25.96875 C19.878908,25.6328108 20.3906216,25.4648438 21.0625,25.4648438 C21.7109407,25.4648438 22.2128888,25.636717 22.5683594,25.9804688 C22.9238299,26.3242205 23.1015625,26.816403 23.1015625,27.4570312 C23.1015625,28.0742218 22.9218768,28.5605451 22.5625,28.9160156 C22.2031232,29.2714862 21.7031282,29.4492188 21.0625,29.4492188 C20.4062467,29.4492188 19.8984393,29.2753924 19.5390625,28.9277344 C19.1796857,28.5800764 19,28.0898469 19,27.4570312 Z"
            fill="#FFFFFF"
          />
        </g>
      </svg>
    </svg>
  );

  render() {
    const { onPrimaryClick, onHide, icon, title, message, primaryLabel, tertiaryLabel, ...props } = this.props;

    const [uiDialogProps, otherProps] = splitComponentProps(
      this.props,
      UIDialog
    );

    return (
      <UIDialog {...uiDialogProps}>
        <UIDialog.Header>{title}</UIDialog.Header>
        <UIDialog.Body>
          <UIDialog.Icon>
            {icon || UIDestructionDialog.defaultIcon}
          </UIDialog.Icon>
          <UIDialog.Message>{message}</UIDialog.Message>
        </UIDialog.Body>
        <UIDialog.Footer>
          <UIButton priority="tertiary" onClick={onHide}>
            {tertiaryLabel}
          </UIButton>
          <UIButton priority="primary" use="danger" onClick={onPrimaryClick}>
            {primaryLabel}
          </UIButton>
        </UIDialog.Footer>
      </UIDialog>
    );
  }
}

UIDestructionDialog.propTypes = {
  ...UIDialog.propTypes,
  /**
   * Title of the dialog. It should be a question to the user.
   */
  title: PropTypes.string,
  /** Label of the primary button. */
  primaryLabel: PropTypes.string,
  /** Callback executed when the primary button is clicked. */
  onPrimaryClick: PropTypes.func,
  /** Tertiary button label */
  tertiaryLabel: PropTypes.string,
  /** Message displayed on the body of the dialog */
  message: PropTypes.node,
  /** Icon displayed oh the body of the dialog */
  icon: PropTypes.node,
};

UIDestructionDialog.defaultProps = {
  ...UIDialog.defaultProps,
  title: 'Do you really want to delete this item?',
  primaryLabel: 'Delete',
  tertiaryLabel: 'Cancel',
  message: (
    <div>
      <p>You are about to delete the item permanently.</p>
      <p>
        <strong>This action can not be undone</strong>.
      </p>
    </div>
  ),
};

export default UIDestructionDialog;
