import React from 'react';
import PropTypes from 'prop-types';
import FormGroup from '../../../shared/components/lead-gen/common/FormGroup';
import I18n from '../../../shared/i18n';

const t = (key, params) => I18n.t(`lead_gen.embeddable_forms.code.forward.modal.form.${key}`, params);

const FromField = (
  {
    getFieldProps,
    getFieldError,
  }
) => {
  return (
    <FormGroup
      label={t('from')}
      errors={getFieldError('forwardEmail.from')}
    >
      <input
        className="form-control"
        disabled
        {...getFieldProps('forwardEmail.from', {
          initialValue: window.currentUserEmail,
          rules: [
            {
              required: true,
              message: t('error.empty'),
            },
          ],
        })}
      />
    </FormGroup>
  );
};

FromField.propTypes = {
  getFieldProps: PropTypes.func,
  getFieldError: PropTypes.func,
};

FromField.defaultProps = {
  getFieldProps: () => {},
  getFieldError: () => {},
};

export default FromField;
