import axios from './RDSMAxiosClient.js';

class EventTracking {
  static trackEvent(eventType, eventOwnerId, properties) {
    let requestPromise;

      return axios.post(window.Routes.event_tracking_path({
        event_type: eventType,
        event_owner_id: eventOwnerId,
        properties: properties || {},
      })).then(response => response);
  }
}

export default EventTracking;
