import merge from 'lodash/merge';

export const DANGER_TYPE = 'danger';
export const SUCCESS_TYPE = 'success';

const defaultOptions = {
  ele: document.body,
  type: 'info',
  offset: {
    from: 'bottom',
    amount: 20,
  },
  align: 'right',
  width: 250,
  delay: 4000,
  allow_dismiss: true,
  stackup_spacing: 10,
};

const getPositionOffset = ({ offset, stackup_spacing }) => {
  let offsetAmount = offset.amount;

  const growls = document.getElementsByClassName('bootstrap-growl');
  [...growls].forEach((growl) => {
    const offsetItems = parseInt(growl.style[offset.from], 10) +
      growl.offsetHeight +
      stackup_spacing; // eslint-disable-line

    offsetAmount = Math.max(offsetAmount, offsetItems);
  });

  return `${offsetAmount}px`;
};

const getAlignment = (alert, { align }) => {
  switch (align) {
    case 'center':
      return {
        left: '50%',
        marginLeft: `-${(alert.offsetWidth / 2)}px`,
      };
    case 'left':
      return {
        left: '20px',
      };
    default:
      return {
        right: '20px',
      };
  }
};

const show = (alert, customOptions) => {
  merge(alert.style, getAlignment(alert, customOptions));

  alert.classList.add('fade-in');

  if (customOptions.delay > 0) {
    setTimeout(() => {
      alert.classList.add('fade-out');
      setTimeout(() => { alert.remove(); }, 410);
    }, customOptions.delay);
  }
};

const setCss = (alert, customOptions) => {
  const css = {
    position: (customOptions.ele === document.body ? 'fixed' : 'absolute'),
    zIndex: '9999',
    margin: 0,
  };

  css[customOptions.offset.from] = getPositionOffset(customOptions);

  if (customOptions.width !== 'auto') {
    css.width = `${customOptions.width}px`;
  }

  merge(alert.style, css);
};

const createAlertElement = (message, customOptions) => {
  const alert = document.createElement('div');
  alert.setAttribute('class', `bootstrap-growl alert alert-${customOptions.type}`);

  if (customOptions.allow_dismiss) { // eslint-disable-line
    alert.innerHTML = '<span class="close" data-dismiss="alert">&times;</span>';
  }

  alert.innerHTML += message;

  setCss(alert, customOptions);

  return alert;
};

const now = (message, options) => {
  const customOptions = merge(defaultOptions, options);
  const alert = createAlertElement(message, customOptions);

  customOptions.ele.appendChild(alert);

  show(alert, customOptions);
};

const set = (text, options) => {
  sessionStorage.setItem('flash_message', JSON.stringify({ text, options }));
};

const get = () => {
  const data = sessionStorage.getItem('flash_message');

  if (data != null) {
    const message = JSON.parse(data);

    now(message.text, message.options);
    sessionStorage.removeItem('flash_message');
  }
};

export default {
  now,
  get,
  set,
};

