import React, { Component } from 'react';
import PropTypes from 'prop-types';
import TemplateCard from './TemplateCard';
import Author from './Author';

class TemplatesList extends Component {

  renderTemplates() {
    const { templates } = this.props;

    return (
      <div className="templates-list-cards">
        {templates.map(template => (
          <div key={template.id} className="templates-list-cards-item col-md-4">
            {this.props.card(template)}
          </div>
        ))}
      </div>
    );
  }

  render() {
    const { children } = this.props;

    return (
      <div className="templates-list">
        <div className="row row-gutter-sm">
          <div className="col-md-12 col-lg-12">
            {this.renderTemplates()}
          </div>
        </div>
        {children}
      </div>
    );
  }
}

TemplatesList.Card = TemplateCard;
TemplatesList.Card.Author = Author;

TemplatesList.propTypes = {
  templates: PropTypes.arrayOf(
    PropTypes.shape({
      author_country: PropTypes.string,
      author_name: PropTypes.string,
      category: PropTypes.string,
      id: PropTypes.number,
      thumbnail: PropTypes.string,
      title: PropTypes.string,
    })
  ),
  card: PropTypes.func.isRequired,
  children: PropTypes.node,
};

TemplatesList.defaultProps = {
  templates: [],
  children: undefined,
};

export default TemplatesList;
