import React, { Component } from 'react';
import PropTypes from 'prop-types';
import FormActionSelect from '../../../../shared/utils/FormActionSelect';
import {
  redirectActionConfig,
  downloadActionConfig,
  noneActionConfig,
} from './config';
import I18n from '../../../../shared/i18n';

const t = (key, params) => I18n.t(`lead_gen.embeddable_forms.conversion_tab.actions.${key}`, params);

class FormActions extends Component {
  render() {
    const { formAction } = this.props;
    return (
      <div id="form-actions-row" className="row">
        <div className="col-sm-3">
          <div>
            <h5>{t('title')}</h5>
          </div>
        </div>
        <div className="col-sm-8">
          <FormActionSelect
            {...this.props}
            configs={[redirectActionConfig, downloadActionConfig, noneActionConfig]}
            formActionInputName="form-actions"
            activePane={formAction}
          />
        </div>
      </div>
    );
  }
}

FormActions.propTypes = {
  formAction: PropTypes.string,
};

FormActions.defaultProps = {
  formAction: 'none',
};

export default FormActions;
