import React from 'react';
import elementType from 'prop-types-extra/lib/elementType';
import cs from 'classnames';
import PropTypes from 'prop-types';

const UIModalFooter = props => {
  const { as: Component, className, ...rest } = props;

  const classes = cs('footer', className);

  return <Component {...rest} className={classes} />;
};

UIModalFooter.propTypes = {
  /** Custom DOM element */
  as: elementType,
  /** Additional classes */
  className: PropTypes.string,
};
UIModalFooter.defaultProps = {
  /** Custom DOM element */
  as: 'div',
};

export default UIModalFooter;
