import React from 'react';
import PropTypes from 'prop-types';
import { UIDialog, UIButton } from '@resultadosdigitais/tangram-react';
import I18n from '../i18n';

const t = (key, params) => I18n.t(`javascripts.v2.dirty_modal.${key}`, params);

class DirtyModal extends React.Component {
  constructor(props) {
    super(props);

    this.closeModal = this.closeModal.bind(this);
    this.onExitWithoutSave = this.onExitWithoutSave.bind(this);
    this.onExitAndSave = this.onExitAndSave.bind(this);
  }

  onExitWithoutSave() {
    const { onExitWithoutSave } = this.props;
    onExitWithoutSave();

    this.closeModal();
  }

  onExitAndSave() {
    const { onExitAndSave } = this.props;
    onExitAndSave();

    this.closeModal();
  }

  closeModal() {
    const { onClose } = this.props;
    onClose();
  }

  render() {
    const {
      children, exitAndSaveMessage, exitWithoutSaveMessage, show,
    } = this.props;

    return (
      <UIDialog show={show} onHide={this.closeModal}>
        <UIDialog.Header>{t('title')}</UIDialog.Header>
        <UIDialog.Body>
          {children}
          <p>{t('dialog')}</p>
        </UIDialog.Body>
        <UIDialog.Footer>
          <UIButton priority="tertiary" onClick={this.closeModal}>
            {t('cancel')}
          </UIButton>
          <UIButton priority="secondary" onClick={this.onExitWithoutSave}>
            {exitWithoutSaveMessage}
          </UIButton>
          <UIButton priority="primary" onClick={this.onExitAndSave}>
            {exitAndSaveMessage}
          </UIButton>
        </UIDialog.Footer>
      </UIDialog>
    );
  }
}

DirtyModal.propTypes = {
  // eslint-disable-next-line react/no-unused-prop-types
  show: PropTypes.bool,
  onClose: PropTypes.func,
  onExitWithoutSave: PropTypes.func,
  onExitAndSave: PropTypes.func,
  children: PropTypes.node,
  exitAndSaveMessage: PropTypes.string,
  exitWithoutSaveMessage: PropTypes.string,
};

DirtyModal.defaultProps = {
  show: false,
  onClose: () => {},
  onExitWithoutSave: () => {},
  onExitAndSave: () => {},
  children: undefined,
  exitAndSaveMessage: t('exit_and_save'),
  exitWithoutSaveMessage: t('exit_without_save'),
};

export default DirtyModal;
