class CodeSnippet {
  constructor(urlSlug, analyticsUAID, rdStaticCloudFront) {
    this.urlSlug = urlSlug;
    this.analyticsUAID = analyticsUAID;
    this.rdStaticCloudFront = rdStaticCloudFront;
  }

  mainDiv() {
    return `<div role="main" id="${this.urlSlug}"></div>\n`;
  }

  scriptInclude() {
    return `<script type="text/javascript" src="${this.rdStaticCloudFront}js/rdstation-forms/stable/rdstation-forms.min.js"></script>\n`;
  }

  scriptInstantiation() {
    return (
      `<script type="text/javascript"> new RDStationForms('${this.urlSlug}', '${this.analyticsUAID}').createForm();</script>`
    );
  }

  print() {
    return this.mainDiv()
      + this.scriptInclude()
      + this.scriptInstantiation();
  }
}

export default CodeSnippet;
