import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';

const Loading = ({ width, className }) => {
  return (
    <svg
      width={width} className={classNames('spin', className)}
      xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"
      preserveAspectRatio="xMidYMid"
    >
      <circle
        cx="50" cy="50" fill="none" ng-attr-stroke="{{config.color}}"
        ng-attr-stroke-width="{{config.width}}" ng-attr-r="{{config.radius}}"
        ng-attr-stroke-dasharray="{{config.dasharray}}" stroke="#ddd" strokeWidth="10"
        r="36" strokeDasharray="164.93361431346415 56.97787143782138"
        transform="rotate(31.1928 50 50)"
      />
    </svg>
  );
};

Loading.propTypes = {
  width: PropTypes.number,
  className: PropTypes.string,
};

Loading.defaultProps = {
  width: null,
  className: null,
};

export default Loading;
