import React, { Component } from 'react';
import PropTypes from 'prop-types';
import DoubleOptInConfigurationSection from '../../../shared/utils/double-opt-in/components/double-opt-in-configuration-section';
import I18n from '../../../shared/i18n';

const t = (key, params) => I18n.t(`lead_gen.embeddable_forms.conversion_tab.double_opt_in.${key}`, params);

class DoubleOptIn extends Component {
  render() {
    const { update, doubleOptIn } = this.props;
    const {
      active,
      name,
      email,
      language,
      pageDisplayName,
      formBaseType,
    } = doubleOptIn;

    return (
      <div id="double-opt-in-row" className="row">
        <div className="col-sm-3">
          <div>
            <h5>
              {t('title')}
              <br />
              <span className="text-muted">
                {t('subtitle')}
              </span>
            </h5>
          </div>
        </div>
        <div className="col-sm-8">
          <DoubleOptInConfigurationSection
            formBaseType={formBaseType}
            active={active}
            language={language}
            pageDisplayName={pageDisplayName}
            name={name}
            email={email}
            update={update}
          />
        </div>
      </div>
    );
  }
}

DoubleOptIn.propTypes = {
  doubleOptIn: PropTypes.shape({
    active: PropTypes.bool,
    name: PropTypes.string,
    email: PropTypes.string,
    language: PropTypes.string,
    pageDisplayName: PropTypes.string,
    formBaseType: PropTypes.string,
  }),
  update: PropTypes.func,
};

DoubleOptIn.defaultProps = {
  doubleOptIn: {
    active: false,
    name: '',
    email: '',
    language: '',
    pageDisplayName: '',
    formBaseType: '',
  },
  update: () => {},
};

export default DoubleOptIn;
