import React from 'react';
import cs from 'classnames';
import PropTypes from 'prop-types';

const UIDialogIcon = props => {
    const { className, ...rest } = props;

    const classes = cs('dialog-icon', className);

    return (
      <div {...rest} className={classes} />
    )
};

UIDialogIcon.propTypes = {
  /** Additional classes to be passed to the element */
  className: PropTypes.string,
};

export default UIDialogIcon;