import React from 'react';
import I18n from './I18n';

export default (Component, translation) => {
  class Enhanced extends React.Component {

    constructor(props) {
      super(props);
      this.i18nProps = this.getI18nProps(this.props)
    }

    getI18nProps(props) {
      const i18n = new I18n(translation);
      const ts = {};
      Component.i18nKeys.forEach(key => {
        ts[key] = props[key] === Component.defaultProps[key] ? i18n.t(key) : props[key];
      });
      return ts;
    };

    render() {
      return <Component {...this.props} {...this.i18nProps} />
    }
  }

  const name = Component.displayName || Component.name || 'Component';

  Enhanced.displayName = `withI18n(${name})`;
  Enhanced.propTypes = Component.propTypes;
  Enhanced.defaultProps = Component.defaultProps;

  return Enhanced;
};