import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import SubHeader from '../common/SubHeader';
import MainContent from './MainContent';
import * as actions from './actions';
import { templateType } from '../../shapes';
import CreatingModal from './CreatingModal';

export class Templates extends Component {

  componentDidMount() {
    this.props.loadTemplates();
  }

  render() {
    return (
      <div className="templates">
        <SubHeader activeStep={0} />
        <MainContent
          loading={this.props.loading}
          templates={this.props.templates}
          toggleShowModal={this.props.toggleShowModal}
        />
        <CreatingModal
          loading={this.props.loading}
          show={this.props.showModal}
          onHide={this.props.toggleShowModal}
          onCreate={this.props.createEmbeddable}
          activeTemplateId={this.props.activeTemplateId}
        />
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    templates: state.templatesStep.templates,
    loading: state.templatesStep.loading,
    showModal: state.templatesStep.showModal,
    activeTemplateId: state.templatesStep.activeTemplateId,
  };
};

const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    loadTemplates: () => {
      dispatch(actions.loadTemplates());
    },
    toggleShowModal: (activeTemplateId) => {
      dispatch(actions.toggleShowModal(activeTemplateId));
    },
    createEmbeddable: (info) => {
      dispatch(actions.createEmbeddable(info, ownProps));
    },
  };
};

Templates.propTypes = {
  loading: PropTypes.bool,
  loadTemplates: PropTypes.func.isRequired,
  createEmbeddable: PropTypes.func.isRequired,
  templates: PropTypes.arrayOf(templateType),
  toggleShowModal: PropTypes.func.isRequired,
  showModal: PropTypes.bool,
  activeTemplateId: PropTypes.string,
};

Templates.defaultProps = {
  showModal: false,
  activeTemplateId: '',
  loading: false,
  templates: [],
};

export default connect(mapStateToProps, mapDispatchToProps)(Templates);
