/*
* actions
*/
import embeddablesRepository from './repositories/embeddables';
import { errorMessage, successMessage } from './components/common/messages';
import I18n from './shared/i18n';

export const SET_EMBEDDABLE = 'SET_EMBEDDABLE';
export const SET_DIRTY_MODAL_FOR_UNSAVED_CHANGES = 'SET_DIRTY_MODAL_FOR_UNSAVED_CHANGES';
export const CLEAN_DIRTY_MODAL_CONFIG = 'CLEAN_DIRTY_MODAL_CONFIG';
export const SHOW_DIRTY_MODAL = 'SHOW_DIRTY_MODAL';
export const CLOSE_DIRTY_MODAL = 'CLOSE_DIRTY_MODAL';
export const LOAD_EMBEDDABLE_REQUEST = 'LOAD_EMBEDDABLE_REQUEST';
export const LOAD_EMBEDDABLE_SUCCESS = 'LOAD_EMBEDDABLE_SUCCESS';
export const LOAD_EMBEDDABLE_FAILURE = 'LOAD_EMBEDDABLE_FAILURE';
export const UPDATE_EMBEDDABLE_REQUEST = 'UPDATE_EMBEDDABLE_REQUEST';
export const UPDATE_EMBEDDABLE_SUCCESS = 'UPDATE_EMBEDDABLE_SUCCESS';
export const UPDATE_EMBEDDABLE_FAILURE = 'UPDATE_EMBEDDABLE_FAILURE';
export const UPDATE_CONFIGURATION_REQUEST = 'UPDATE_CONFIGURATION_REQUEST';
export const UPDATE_CONFIGURATION_FAILURE = 'UPDATE_CONFIGURATION_FAILURE';
export const UPDATE_CONFIGURATION_SUCCESS = 'UPDATE_CONFIGURATION_SUCCESS';


const t = (key, params) => I18n.t(`lead_gen.embeddable_forms.${key}`, params);

/*
* action creators
*/

export const setEmbeddable = embeddable => ({
  type: SET_EMBEDDABLE,
  embeddable,
});

export const setDirtyModalForUnsavedChanges = (onSaveForCurrentStepMethod,
  checkForUnsavedChangesAtCurrentStepMethod) => ({
  type: SET_DIRTY_MODAL_FOR_UNSAVED_CHANGES,
  onSaveForCurrentStepMethod,
  checkForUnsavedChangesAtCurrentStepMethod,
});

export const cleanDirtyModalConfig = () => ({
  type: CLEAN_DIRTY_MODAL_CONFIG,
  onSaveForCurrentStepMethod: undefined,
  checkForUnsavedChangesAtCurrentStepMethod: undefined,
});

export const showDirtyModal = callbackForOriginalActionMethod => ({
  type: SHOW_DIRTY_MODAL,
  callbackForOriginalActionMethod,
});

export const closeDirtyModal = () => ({
  type: CLOSE_DIRTY_MODAL,
});

export const loadEmbeddableRequest = () => ({
  type: LOAD_EMBEDDABLE_REQUEST,
});

export const loadEmbeddableFailure = error => ({
  type: LOAD_EMBEDDABLE_FAILURE,
  error,
});

export const loadEmbeddableSuccess = embeddable => ({
  type: LOAD_EMBEDDABLE_SUCCESS,
  embeddable,
  loading: false,
});

export const loadEmbeddable = embeddableId => (dispatch) => {
  dispatch(loadEmbeddableRequest());

  return embeddablesRepository.get(embeddableId).then(
    response => dispatch(loadEmbeddableSuccess(response.data)),
    error => dispatch(loadEmbeddableFailure(error))
  );
};

export const updateEmbeddableRequest = embeddableInfo => ({
  type: UPDATE_EMBEDDABLE_REQUEST,
  embeddableInfo,
});

export const updateEmbeddableFailure = error => ({
  type: UPDATE_EMBEDDABLE_FAILURE,
  error,
});

export const updateEmbeddableSuccess = embeddableInfo => ({
  type: UPDATE_EMBEDDABLE_SUCCESS,
  embeddableInfo,
});

export const updateEmbeddable = (embeddableInfo, ownProps, redirect) => (dispatch) => {
  dispatch(updateEmbeddableRequest(embeddableInfo));

  return embeddablesRepository.updateContent(
    embeddableInfo.id,
    embeddableInfo.content_id,
    embeddableInfo.payload
  ).then(() => {
    dispatch(updateEmbeddableSuccess(embeddableInfo));
    successMessage(t('save_successfull'));
    if (redirect) ownProps.history.replace(`/${embeddableInfo.id}/editar/conversao`);
  },
  (error) => {
    dispatch(updateEmbeddableFailure(error));
    errorMessage(t('save_error'));
  });
};

export const updateConfigurationRequest = () => ({
  type: UPDATE_CONFIGURATION_REQUEST,
});

export const updateConfigurationFailure = error => ({
  type: UPDATE_CONFIGURATION_FAILURE,
  error,
});

export const updateConfigurationSuccess = (embeddable, landingPageAction) => ({
  type: UPDATE_CONFIGURATION_SUCCESS,
  landingPageAction,
  embeddable,
});

export const updateConfiguration = (embeddable, config, ownProps, redirect) => (dispatch) => {
  dispatch(updateConfigurationRequest());

  return embeddablesRepository.updateEmbeddable(embeddable.id, config).then(() => {
    const landingPageAction = config.landing_page_embeddable.landing_page_action_attributes;
    dispatch(updateConfigurationSuccess(embeddable, landingPageAction));
    successMessage(t('save_successfull'));
    if (redirect) ownProps.history.push(`/${embeddable.id}/editar/codigo`);
  },
  (error) => {
    dispatch(updateConfigurationFailure(error));
    errorMessage(t('save_error'));
  });
};

export const previewEmbeddable = (payload) => {
  return embeddablesRepository.previewEmbeddable(payload);
};
