import React from 'react';
import PropTypes from 'prop-types';
import splitComponentProps from 'react-bootstrap/lib/utils/splitComponentProps';

import UIDialog from '../UIDialog/UIDialog';

import UIButton from '../UIButton/UIButton';

/**
 It is used when the person has unsaved changes in some edition or creation and does some action that can cause loss of the changes.

 ***Context of application:**
 - Change tab
 - Exit edit area
 */

class UIChangesDialog extends React.Component {
  static i18nKeys = [
    'title',
    'primaryLabel',
    'secondaryLabel',
    'tertiaryLabel',
    'message',
  ];

  static defaultIcon = (
    <svg width="40px" height="40px">
      <svg
        width="38px"
        height="34px"
        x="1"
        y="3"
        viewBox="0 0 42 38"
        version="1.1"
        xmlns="http://www.w3.org/2000/svg"
        xmlnsXlink="http://www.w3.org/1999/xlink"
      >
        <title>Attention Icon</title>
        <g>
          <path
            d="M22.7504831,3.16754079 L40.3601364,35.0326278 C40.8944013,35.9993929 40.5437907,37.2162181 39.5770256,37.7504831 C39.2808675,37.9141494 38.9480263,38 38.6096533,38 L3.39034667,38 C2.28577717,38 1.39034667,37.1045695 1.39034667,36 C1.39034667,35.661627 1.4761973,35.3287858 1.6398636,35.0326278 L19.2495169,3.16754079 C19.7837819,2.20077569 21.0006071,1.85016502 21.9673722,2.38442995 C22.2969196,2.56654826 22.5683648,2.83799338 22.7504831,3.16754079 Z"
            fill="#FFBD02"
          />
          <path
            d="M22.5535437,24.498321 L19.9316498,24.498321 L19.383631,14 L23.1015625,14 L22.5535437,24.498321 Z M19.3406491,28.1732707 C19.3406491,27.5715215 19.5018295,27.1166346 19.8241951,26.8085964 C20.1465608,26.5005581 20.6157748,26.3465413 21.2318513,26.3465413 C21.8264368,26.3465413 22.2866963,26.5041399 22.6126438,26.8193418 C22.9385912,27.1345438 23.1015625,27.5858489 23.1015625,28.1732707 C23.1015625,28.7392014 22.9368003,29.1851338 22.607271,29.5110813 C22.2777417,29.8370287 21.8192731,30 21.2318513,30 C20.6301021,30 20.1644699,29.8406105 19.8349406,29.5218267 C19.5054113,29.203043 19.3406491,28.7535288 19.3406491,28.1732707 Z"
            fill="#FFFFFF"
          />
        </g>
      </svg>
    </svg>
  );

  render() {
    const [uiDialogProps, props] = splitComponentProps(
      this.props,
      UIDialog
    );

    const {
      onPrimaryClick,
      onSecondaryClick,
      showSecondary,
      icon,
      title,
      message,
      tertiaryLabel,
      secondaryLabel,
      primaryLabel,
    } = props;

    return (
      <UIDialog {...uiDialogProps}>
        <UIDialog.Header>{title}</UIDialog.Header>
        <UIDialog.Body>
          <UIDialog.Icon>{icon || UIChangesDialog.defaultIcon}</UIDialog.Icon>
          <UIDialog.Message>{message}</UIDialog.Message>
        </UIDialog.Body>
        <UIDialog.Footer>
          <UIButton priority="tertiary" onClick={uiDialogProps.onHide}>
            {tertiaryLabel}
          </UIButton>
          {showSecondary && (
            <UIButton priority="secondary" onClick={onSecondaryClick}>
              {secondaryLabel}
            </UIButton>
          )}
          <UIButton priority="primary" onClick={onPrimaryClick}>
            {primaryLabel}
          </UIButton>
        </UIDialog.Footer>
      </UIDialog>
    );
  }
}

UIChangesDialog.propTypes = {
  ...UIDialog.propTypes,
  /**
   * Title of the dialog. It should be a question to the user.
   */
  title: PropTypes.string,
  /** Label of the primary button. */
  primaryLabel: PropTypes.string,
  /** Callback executed when the primary button is clicked. */
  onPrimaryClick: PropTypes.func,
  /** If false the secondary button is not shown. */
  showSecondary: PropTypes.bool,
  /** Label of the secondary button. */
  secondaryLabel: PropTypes.string,
  /** Callback executed when the secondary button is clicked. */
  onSecondaryClick: PropTypes.func,
  /** Tertiary button label */
  tertiaryLabel: PropTypes.string,
  /** Message displayed on the body of the dialog */
  message: PropTypes.node,
  /** Icon displayed oh the body of the dialog */
  icon: PropTypes.node,
};

UIChangesDialog.defaultProps = {
  ...UIDialog.defaultProps,
  title: 'Do you want to save the changes you made?',
  primaryLabel: 'Save and Exit',
  showSecondary: true,
  secondaryLabel: 'Exit without save',
  tertiaryLabel: 'Cancel',
  message: <p>You may lose the changes made to the item.</p>,
};

export default UIChangesDialog;
