import React, { useContext } from 'react';
import Context from './context';

const useFeatureFlag = (entity, namespace, flag) => {
  const flagStore = useContext(Context);
  return flagStore(entity, namespace, flag);
};

const withFeatureFlagsMapping = flagMap => (WrappedComponent) => {
  const mapFlagsToProps = (flagStore) => {
    const props = {};
    Object.entries(flagMap).forEach(([propName, flag]) => {
      props[propName] = flagStore(...flag);
    });
    return props;
  };
  return props => (
    <Context.Consumer>
      {flagStore => (
        <WrappedComponent {...mapFlagsToProps(flagStore)} {...props} />
      )}
    </Context.Consumer>
  );
};

export { useFeatureFlag, withFeatureFlagsMapping };
