import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import cs from 'classnames';
import isEqual from 'lodash/isEqual';
import LoadingButton from '../../shared/utils/react/LoadingButton';
import SubHeader from '../common/SubHeader';
import { embeddableType } from '../../shapes';
import MainContent from './MainContent';
import * as actions from '../../actions';
import * as editorActions from './actions';
import { trackEvent } from '../common/events';
import I18n from '../../shared/i18n';

const t = (key, params) => I18n.t(`lead_gen.embeddable_forms.${key}`, params);

export class Editor extends React.Component {
  componentDidMount() {
    this.props.loadEmbeddable(this.props.embeddableId);
    window.embeddableId = this.props.embeddableId;
    this.props.setDirtyModalForUnsavedChanges(
      this.handleSaveBtnClick.bind(this),
      () => {
        const editorRef = window.bricksEditor || this.editor;
        return editorRef.isDirty();
      }
    );
  }

  hasTemplateChanged(editorRef) {
    const {
      embeddable: { payload },
    } = this.props;

    return !isEqual(payload, editorRef.exportDesign());
  }

  saveIfTemplateHasChanged() {
    const editorRef = window.bricksEditor || this.editor; // for tests purposes
    let savingPromise;

    if (!this.props.loading && this.hasTemplateChanged(editorRef)) {
      const embeddableInfo = {
        ...this.props.embeddable,
        payload: editorRef.exportDesign(),
      };
      savingPromise = this.props.updateEmbeddable(embeddableInfo);
    }
    return savingPromise;
  }

  handleSaveBtnClick() {
    this.saveIfTemplateHasChanged();
  }

  handleSaveAndForwardBtnClick() {
    const editorRef = window.bricksEditor || this.editor; // for tests purposes

    if (!this.props.loading) {
      trackEvent(
        'Save and continue',
        { embeddable_id: this.props.embeddableId }
      );
      const embeddableInfo = {
        ...this.props.embeddable,
        payload: editorRef.exportDesign(),
      };
      this.props.updateEmbeddable(embeddableInfo, true);
    }
  }

  handlePreviewBtnClick() {
    const savingPromise = this.saveIfTemplateHasChanged() || new Promise(resolve => resolve());
    savingPromise.then(() => {
      const { embeddable, onPreview } = this.props;
      onPreview(embeddable);
    });
  }

  renderSubHeader() {
    const {
      embeddableId,
      loading,
      updating,
      iframeLoaded,
    } = this.props;

    return (
      <SubHeader activeStep={1} embeddableId={embeddableId}>
        <React.Fragment>
          <button
            type="button"
            className={cs('btn btn-link btn-sm subheader-btn btn-preview', {
              disabled: updating || loading,
            })}
            onClick={() => this.handlePreviewBtnClick()}
          >
            {t('preview')}
          </button>
          <button
            className={cs('btn btn-link btn-sm subheader-btn btn-save', {
              disabled: updating || loading || !iframeLoaded,
            })}
            onClick={() => this.handleSaveBtnClick()}
          >
            {t('save')}
          </button>
          <LoadingButton
            className={cs(
              'btn btn-primary btn-sm go-forward editor subheader-btn',
              {
                disabled: updating || loading || !iframeLoaded,
              }
            )}
            bsStyle="primary"
            disabled={updating || loading || !iframeLoaded}
            onClick={() => this.handleSaveAndForwardBtnClick()}
            isLoading={updating}
            type="submit"
          >
            {t('save_and_forward')}
          </LoadingButton>
        </React.Fragment>
      </SubHeader>
    );
  }

  render() {
    const {
      embeddable,
      loading,
      iframeLoaded,
      setIframeLoaded,
      setIframeNotLoaded,
    } = this.props;

    return (
      <React.Fragment>
        {this.renderSubHeader()}
        <div className="container main-content">
          <MainContent
            embeddable={embeddable}
            loading={loading}
            iframeLoaded={iframeLoaded}
            setIframeLoaded={setIframeLoaded}
            setIframeNotLoaded={setIframeNotLoaded}
          />
        </div>
      </React.Fragment>
    );
  }
}

Editor.propTypes = {
  embeddable: embeddableType,
  embeddableId: PropTypes.string,
  loading: PropTypes.bool,
  updating: PropTypes.bool,
  loadEmbeddable: PropTypes.func.isRequired,
  setIframeLoaded: PropTypes.func.isRequired,
  setIframeNotLoaded: PropTypes.func.isRequired,
  updateEmbeddable: PropTypes.func.isRequired,
  iframeLoaded: PropTypes.bool,
  setDirtyModalForUnsavedChanges: PropTypes.func.isRequired,
  onPreview: PropTypes.func,
};

Editor.defaultProps = {
  embeddable: null,
  embeddableId: null,
  loading: false,
  updating: false,
  iframeLoaded: false,
  onPreview: () => {},
};

const mapStateToProps = (state) => {
  return {
    embeddable: state.embeddableApp.embeddable,
    loading: state.embeddableApp.loading,
    iframeLoaded: state.editorStep.iframeLoaded,
    updating: state.editorStep.updating,
  };
};

const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    loadEmbeddable: (id) => {
      dispatch(actions.loadEmbeddable(id));
    },
    setIframeLoaded: () => {
      dispatch(editorActions.setIframeLoaded());
    },
    setIframeNotLoaded: () => {
      dispatch(editorActions.setIframeNotLoaded());
    },
    updateEmbeddable: (embeddable, redirect) => {
      return dispatch(actions.updateEmbeddable(embeddable, ownProps, redirect));
    },
    setDirtyModalForUnsavedChanges: (onSaveForCurrentStepMethod, checkForUnsavedChangesAtCurrentStepMethod) => {
      dispatch(actions.setDirtyModalForUnsavedChanges(onSaveForCurrentStepMethod, checkForUnsavedChangesAtCurrentStepMethod));
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Editor);
