import React from 'react';
import { OverlayTrigger, Popover } from 'react-bootstrap';
import PropTypes from 'prop-types';
import I18n from '../../../shared/i18n';

const t = (key, params) => I18n.t(`lead_gen.embeddable_forms.modal.form.${key}`, params);

const popover = info => (
  <Popover id="popover-change-form-name">
    {info}
  </Popover>
);

const Form = (props) => {
  const { title, handleChange } = props;
  return (
    <div>
      <div className="form-group">
        <img className="img-responsive center-block" src="/assets/icons/empty-index/landing_page.png" alt={I18n.t('lead_gen.embeddable_forms.modal.header.title')} />
      </div>
      <div className="form-group">
        <label htmlFor="embeddable_form_title">
          <span>{t('name.label')}</span>
          &nbsp;
          <OverlayTrigger placement="right" overlay={popover(t('name.hint'))}>
            <i className="xicon-info-circle" />
          </OverlayTrigger>
        </label>
        <input
          id="embeddable_form_title"
          name="embeddable_form_title"
          className="form-control"
          autoComplete="off"
          required="required"
          type="text"
          onChange={handleChange}
          defaultValue={title}
        />
      </div>
    </div>
  );
};

Form.propTypes = {
  title: PropTypes.string,
  handleChange: PropTypes.func.isRequired,
};

Form.defaultProps = {
  title: '',
};

export default Form;
