import React from 'react';
import PropTypes from 'prop-types';
import Logo from './logo';
import Loading from './loading';
import CustomButton from './CustomButton';
import PreviewIframe from './PreviewIframe';
import I18n from '../../../../i18n';

const t = (key, params) => I18n.t(`landing_pages.builder.preview_modal.${key}`, params);

const FORBIDDEN_SELECTORS = [
  {
    selector: 'a[href="*||UNSUBSCRIBE_LINK||*"]',
    message: 'unsubscribe_message',
  },
  {
    selector: 'a[href="*||WEB_PREVIEW_LINK||*"]',
    message: 'view_in_browser_message',
  },
];

class PreviewModal extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      mode: PreviewModal.DEVICES.DESKTOP,
      lastProps: props,
    };

    this.handleCloseBtnClick = this.handleCloseBtnClick.bind(this);
  }

  componentDidMount() {
    const { onHide } = this.props;
    window.onpopstate = onHide;
  }

  static getDerivedStateFromProps(props, state) {
    if (props.show && !state.lastProps.show) {
      // eslint-disable-next-line no-restricted-globals
      history.pushState(null, null, location.href);
    }

    return { lastProps: props };
  }

  handleCloseBtnClick() {
    const { onHide } = this.props;
    // eslint-disable-next-line no-restricted-globals
    history.go(-1);
    onHide();
  }

  render() {
    const { mode } = this.state;
    const {
      feature, content, show, title,
    } = this.props;

    const isButtonActive = type => mode === type;
    return (
      <div
        className={`templates-preview-modal ${show ? 'show' : 'hide'} ${mode}`}
      >
        <nav className="header-navbar">
          <div className="header-navbar-container">
            <div className="header-navbar-item">
              <span className="brand-rdstation">
                <span className="sr-only">RDStation</span>
                <Logo />
              </span>
            </div>
            <div className="header-navbar-item">
              <p className="header-navbar-text">{feature}</p>
            </div>
            <div className="header-navbar-item">
              <p className="header-navbar-text">
                {`${t('previewing')}: `}
                <strong>
                  <span className="preview-title">{title}</span>
                </strong>
              </p>
            </div>
            <div className="header-navbar-item">
              <div className="btn-group" role="group">
                <CustomButton
                  active={isButtonActive(PreviewModal.DEVICES.DESKTOP)}
                  label="Desktop"
                  onClick={() => this.setState({ mode: PreviewModal.DEVICES.DESKTOP })
                  }
                />
                <CustomButton
                  active={isButtonActive(PreviewModal.DEVICES.MOBILE)}
                  label="Mobile"
                  onClick={() => this.setState({ mode: PreviewModal.DEVICES.MOBILE })
                  }
                />
              </div>
            </div>
            <div className="header-navbar-item right">
              <button
                type="button"
                className="btn btn-sm header-navbar-link close-preview"
                onClick={this.handleCloseBtnClick}
              >
                {t('close')}
              </button>
            </div>
          </div>
        </nav>
        <section className="content content-builder template-fullsize">
          {content && show
            ? (
              <PreviewIframe
                content={content}
                forbiddenSelectors={FORBIDDEN_SELECTORS}
              />
            ) : <Loading />}
        </section>
      </div>
    );
  }
}

PreviewModal.DEVICES = {
  DESKTOP: 'desktop',
  MOBILE: 'mobile',
};

PreviewModal.propTypes = {
  content: PropTypes.string,
  feature: PropTypes.string,
  onHide: PropTypes.func,
  show: PropTypes.bool,
  title: PropTypes.string,
};

PreviewModal.defaultProps = {
  content: undefined,
  feature: 'Landing Pages',
  show: false,
  title: '',
  onHide: () => {},
};

export default PreviewModal;
