import React from 'react';
import PropTypes from 'prop-types';
import FlashMessage from '../../../../utils/flash-message';
import I18n from '../../../../i18n';

const t = (key, params) => I18n.t(`javascripts.v2.email_preview.${key}`, params);

class PreviewIframe extends React.Component {
  componentDidMount() {
    const { content } = this.props;
    this.updateIframeContent(content);
  }

  setIframeRef(ref) {
    this.iframe = ref;
  }

  setForbiddenLinks(iframe) {
    const { forbiddenSelectors } = this.props;
    const addClickListener = (item) => {
      const target = iframe.querySelector(item.selector);
      if (target) {
        target.addEventListener('click', this.preventForbiddenLinks(item.message));
      }
    };
    forbiddenSelectors.forEach(addClickListener);
  }

  preventForbiddenLinks(message) {
    return (e) => {
      e.preventDefault();
      this.showMessagesForForbiddenLinks(message);
    };
  }

  showMessagesForForbiddenLinks(message) {
    FlashMessage.now(t(message), {
      type: 'danger',
      width: 460,
    });
  }

  updateIframeContent(content) {
    const { forbiddenSelectors } = this.props;
    const iframe = this.iframe.contentDocument;

    iframe.open();
    iframe.write(content);
    iframe.close();

    if (forbiddenSelectors) {
      this.setForbiddenLinks(iframe);
    }
  }

  render() {
    const { content } = this.props;

    return (
      <iframe
        title="Preview frame"
        ref={ref => this.setIframeRef(ref)}
        className={`preview preview-window-full ${
          content ? 'show' : 'hide'
        }`}
        src="about:blank"
        border="0"
        style={{
          backgroundColor: '#e5e5e5',
          display: 'block',
          margin: '0 auto',
        }}
        width="100%"
        height="100%"
        frameBorder="0"
        bgcolor="#e5e5e5"
      />
    );
  }
}

PreviewIframe.propTypes = {
  content: PropTypes.string.isRequired,
  forbiddenSelectors: PropTypes.arrayOf(
    PropTypes.shape({
      selector: PropTypes.string,
      message: PropTypes.string,
    })
  ),
};

PreviewIframe.defaultProps = {
  forbiddenSelectors: [],
};

export default PreviewIframe;
