import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import cs from 'classnames';
import SubHeader from '../common/SubHeader';
import LoadingButton from '../../shared/utils/react/LoadingButton';
import { embeddableType } from '../../shapes';
import MainContent from './MainContent';
import * as actions from '../../actions';
import { fromState, toState } from './embeddableConfiguration';
import I18n from '../../shared/i18n';

const t = (key, params) => I18n.t(`lead_gen.embeddable_forms.${key}`, params);

export class Configuration extends Component {
  constructor(props) {
    super(props);
    this.state = {
      landingPageActions: {},
      doubleOptIn: {},
      feedbackEmail: {},
      loaded: false,
      isDirty: false,
      captchaStatus: false,
    };
    this.submit = this.submit.bind(this);
    this.update = this.update.bind(this);
    this.setFormRef = this.setFormRef.bind(this);

    this.props.setDirtyModalForUnsavedChanges(
      this.submit.bind(this),
      this.checkForUnsavedChanges.bind(this),
    );
  }

  static getDerivedStateFromProps(nextProps, state) {
    if (nextProps.embeddable && !state.loaded) {
      const {
        landingPageActions,
        doubleOptIn,
        feedbackEmail,
        captchaStatus,
      } = toState(nextProps.embeddable);
      return {
        ...state, landingPageActions, doubleOptIn, feedbackEmail, captchaStatus, loaded: true,
      };
    }
    return null;
  }

  componentDidMount() {
    this.props.loadEmbeddable(this.props.embeddableId);
  }

  setFormRef(ref) {
    this.formRef = ref;
  }

  checkForUnsavedChanges() {
    const { isDirty } = this.state;
    return isDirty;
  }

  handlePreviewBtnClick() {
    const { embeddable, onPreview } = this.props;
    onPreview(embeddable);
  }

  submit(redirect) {
    const { form } = this.formRef.props;
    const { updateConfiguration, embeddable } = this.props;
    const { landingPageActions, doubleOptIn, feedbackEmail, captchaStatus } = this.state;

    form.validateFields((err) => {
      if (err) {
        return;
      }
      const content = fromState(landingPageActions, doubleOptIn, feedbackEmail, captchaStatus);
      updateConfiguration(embeddable, content, redirect);
      this.setState({ isDirty: false });
    });
  }

  update(namespace, property, value) {
    const { landingPageActions, doubleOptIn, feedbackEmail } = this.state;
    let { captchaStatus } = this.state;

    if (namespace === 'doubleOptIn') {
      doubleOptIn[property] = value;
    } else if (namespace === 'feedbackEmail') {
      feedbackEmail[property] = value;
    } else if (namespace === 'captchaStatus') {
      captchaStatus = value;
    } else {
      landingPageActions[property] = value;
    }
    this.setState({
      landingPageActions,
      doubleOptIn,
      feedbackEmail,
      captchaStatus,
      loaded: true,
      isDirty: true,
    });
  }

  renderSubHeader() {
    const {
      embeddableId,
      loading,
      updating,
    } = this.props;

    return (
      <SubHeader activeStep={2} embeddableId={embeddableId}>
        <React.Fragment>
          <button
            className={cs('btn btn-link btn-sm subheader-btn btn-preview', {
              disabled: updating || loading,
            })}
            onClick={() => this.handlePreviewBtnClick()}
          >
            {t('preview')}
          </button>
          <button
            className={cs('btn btn-link btn-sm subheader-btn btn-save', {
              disabled: updating || loading,
            })}
            onClick={() => this.submit(false)}
          >
            {t('save')}
          </button>
          <LoadingButton
            className={cs(
              'btn btn-primary btn-sm go-forward editor subheader-btn',
              {
                disabled: updating || loading,
              }
            )}
            bsStyle="primary"
            disabled={updating || loading}
            onClick={() => this.submit(true)}
            isLoading={updating}
            type="submit"
          >
            {t('save_and_forward')}
          </LoadingButton>
        </React.Fragment>
      </SubHeader>
    );
  }

  render() {
    const {
      embeddable,
      loading,
    } = this.props;

    return (
      <React.Fragment>
        {this.renderSubHeader()}
        <div className="container main-content">
          <MainContent
            embeddable={embeddable}
            loading={loading}
            update={this.update}
            setFormRef={this.setFormRef}
            landingPageActions={this.state.landingPageActions}
            doubleOptIn={this.state.doubleOptIn}
            feedbackEmail={this.state.feedbackEmail}
            captchaStatus={this.state.captchaStatus}
          />
        </div>
      </React.Fragment>
    );
  }
}

Configuration.propTypes = {
  embeddableId: PropTypes.string.isRequired,
  embeddable: embeddableType,
  loading: PropTypes.bool,
  updating: PropTypes.bool,
  loadEmbeddable: PropTypes.func.isRequired,
  updateConfiguration: PropTypes.func,
  setDirtyModalForUnsavedChanges: PropTypes.func.isRequired,
  onPreview: PropTypes.func,
};

Configuration.defaultProps = {
  embeddable: null,
  loading: true,
  updating: false,
  updateConfiguration: () => {},
  onPreview: () => {},
};

const mapStateToProps = (state) => {
  return {
    embeddable: state.embeddableApp.embeddable,
    loading: state.embeddableApp.loading,
    ...state.configurationStep,
  };
};

const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    loadEmbeddable: (id) => {
      dispatch(actions.loadEmbeddable(id));
    },
    updateConfiguration: (embeddable, content, redirect) => {
      dispatch(actions.updateConfiguration(embeddable, content, ownProps, redirect));
    },
    setDirtyModalForUnsavedChanges: (onSaveForCurrentStepMethod, checkForUnsavedChangesAtCurrentStepMethod) => {
      dispatch(actions.setDirtyModalForUnsavedChanges(onSaveForCurrentStepMethod, checkForUnsavedChangesAtCurrentStepMethod));
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Configuration);
