import React from 'react';
import PropTypes from 'prop-types';

const SelectBox = ({ children }) => {
  return (
    <div className="select-box">
      { children }
    </div>
  );
};

SelectBox.propTypes = {
  children: PropTypes.instanceOf(Array),
};

SelectBox.defaultProps = {
  children: [],
};

export default SelectBox;
