import React from 'react';
import cs from 'classnames';

import Modal from '../Modal/Modal';
import Body from '../Modal/UIModalBody';
import Footer from '../Modal/UIModalFooter';
import Header from '../Modal/UIModalHeader';
import Title from '../Modal/UIModalTitle';
import Message from '../Modal/UIDialogMessage';
import Icon from '../Modal/UIDialogIcon';

/**
 * Dialog component to be used when the user needs to make a decision about
 * an action. Only use when is necessary to interrupt the flow of actions
 */

const UIDialog = props => {
  const classes = cs(props.className, 'dialog');
  return <Modal {...props} className={classes} />;
};

UIDialog.propTypes = {
  ...Modal.propTypes,
};

UIDialog.defaultProps = {
  ...Modal.defaultProps,
};

UIDialog.Body = Body;
UIDialog.Header = Header;
UIDialog.Title = Title;
UIDialog.Footer = Footer;
UIDialog.Message = Message;
UIDialog.Icon = Icon;

export default UIDialog;