/* eslint-disable react/no-danger */
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import cs from 'classnames';
import { embeddableType } from '../../shapes';
import CenteredSpinner from '../common/CenteredSpinner';
import Form from './Form';

class MainContent extends Component {
  render() {
    const {
      embeddable,
      loading,
    } = this.props;
    return (
      <section
        className={cs('row row-gutter-sm', {
          'loading-overlay': loading,
        })}
      >
        {loading ? (
          <CenteredSpinner />
        ) : (
          <div className="col-md-12 col-lg-12">
            <Form
              embeddable={embeddable}
              wrappedComponentRef={ref => this.props.setFormRef(ref)}
              landingPageActions={this.props.landingPageActions}
              doubleOptIn={this.props.doubleOptIn}
              feedbackEmail={this.props.feedbackEmail}
              captchaStatus={this.props.captchaStatus}
              update={this.props.update}
            />
          </div>
        )}
      </section>
    );
  }
}

MainContent.propTypes = {
  embeddable: embeddableType,
  landingPageActions: PropTypes.instanceOf(Object).isRequired,
  loading: PropTypes.bool,
  update: PropTypes.func,
  setFormRef: PropTypes.func,
  doubleOptIn: PropTypes.instanceOf(Object),
  feedbackEmail: PropTypes.instanceOf(Object),
  captchaStatus: PropTypes.bool,
};

MainContent.defaultProps = {
  embeddable: null,
  loading: true,
  update: () => {},
  setFormRef: () => {},
  doubleOptIn: PropTypes.instanceOf(Object),
  feedbackEmail: PropTypes.instanceOf(Object),
  captchaStatus: false,
};

export default MainContent;
