import React from 'react';
import PropTypes from 'prop-types';

const UIProgressBar = ({ progress: progressProp }) => {
  let progress = progressProp;
  if (progress < 0) {
    progress = 0
  }
  if (progress > 100) {
    progress = 100
  }
  return (
    <div className="progress">
      <div
        className="progress-bar"
        role="progressbar"
        aria-valuenow={progress}
        aria-valuemin="0"
        aria-valuemax="100"
        style={{ width: `${progress}%` }}
      />
    </div>
  )
};

UIProgressBar.propTypes = {
  progress: (props, propName, componentName) => {
    if(props[propName] < 0) {
       return new Error('Invalid prop `' + propName + '` supplied to' +
        ' `' + componentName + '`. Value has to be greater than or equal to 0')
    }

    if(props[propName] > 100) {
       return new Error('Invalid prop `' + propName + '` supplied to' +
        ' `' + componentName + '`. Value has to be lesser than or equal to 100')
    }
 }
};

UIProgressBar.defaultProps = {
  progress: 0,
};

export default UIProgressBar;
