import React from 'react';
import PropTypes from 'prop-types';
import { PlatformAccount } from '../entities';
import Context from './context';

const FeatureFlagger = {};

// interface FlagStore {
//     (entity: string, namespace: string, flag: string): boolean;
// }

FeatureFlagger.Provider = ({ flagStore, children, entityScope }) => {
  let scopedFlagStore = flagStore;
  if (typeof entityScope !== 'undefined') {
    scopedFlagStore = (namespace, name) => flagStore(entityScope, namespace, name);
  }
  return <Context.Provider value={scopedFlagStore}>{children}</Context.Provider>;
};

FeatureFlagger.Provider.propTypes = {
  flagStore: PropTypes.func.isRequired,
  entityScope: PropTypes.string,
  children: PropTypes.node,
};

FeatureFlagger.Provider.defaultProps = {
  children: null,
  entityScope: PlatformAccount,
};

export default FeatureFlagger;
