/*
* actions
*/
import embeddablesRepository from '../../repositories/embeddables';

export const TOGGLE_SHOW_MODAL = 'TOGGLE_SHOW_MODAL';
export const LOAD_TEMPLATES_REQUEST = 'LOAD_TEMPLATES_REQUEST';
export const LOAD_TEMPLATES_SUCCESS = 'LOAD_TEMPLATES_SUCCESS';
export const LOAD_TEMPLATES_FAILURE = 'LOAD_TEMPLATES_FAILURE';
export const CREATE_EMBEDDABLE_REQUEST = 'CREATE_EMBEDDABLE_REQUEST';
export const CREATE_EMBEDDABLE_SUCCESS = 'CREATE_EMBEDDABLE_SUCCESS';
export const CREATE_EMBEDDABLE_FAILURE = 'CREATE_EMBEDDABLE_FAILURE';

/*
* action creators
*/
export const toggleShowModal = activeTemplateId => ({
  type: TOGGLE_SHOW_MODAL,
  activeTemplateId,
});

export const loadTemplatesRequest = () => ({
  type: LOAD_TEMPLATES_REQUEST,
});

export const loadTemplatesSuccess = templates => ({
  type: LOAD_TEMPLATES_SUCCESS,
  templates,
});

export const loadTemplatesFailure = error => ({
  type: LOAD_TEMPLATES_FAILURE,
  error,
});

export const loadTemplates = () => (dispatch) => {
  dispatch(loadTemplatesRequest());

  return embeddablesRepository.getTemplates().then(
    response => dispatch(loadTemplatesSuccess(response.data)),
    error => dispatch(loadTemplatesFailure(error))
  );
};

export const createEmbeddableRequest = () => ({
  type: CREATE_EMBEDDABLE_REQUEST,
});

export const createEmbeddableSuccess = id => ({
  type: CREATE_EMBEDDABLE_SUCCESS,
  embeddableId: id,
});

export const createEmbeddableFailure = error => ({
  type: CREATE_EMBEDDABLE_FAILURE,
  error,
});

export const createEmbeddable = (info, ownProps) => (dispatch) => {
  dispatch(createEmbeddableRequest());

  return embeddablesRepository.createEmbeddable(info).then(
    (response) => {
      dispatch(createEmbeddableSuccess(response.data.embeddable.id));
      window.location.assign(`${response.data.embeddable.id}/editar/aparencia`);
    },
    error => dispatch(createEmbeddableFailure(error))
  );
};

