import React from 'react';
import cs from 'classnames';

import Modal from '../Modal/Modal';
import Body from '../Modal/UIModalBody';
import Footer from '../Modal/UIModalFooter';
import Header from '../Modal/UIModalHeader';
import Title from '../Modal/UIModalTitle';

/**
 * Modal component to be used when the user needs to interact with a form,
 * edit some sort of data or pre-visualize something.
 * Not to be used as a help page or something else when it is not necessary to
 * interrupt the flow of actions.
 */

const UIModal = props => {
  const classes = cs(props.className, 'modal');
  return <Modal {...props} className={classes} />;
};

UIModal.propTypes = {
  ...Modal.propTypes,
};

UIModal.defaultProps = {
  ...Modal.defaultProps,
};

UIModal.Body = Body;
UIModal.Header = Header;
UIModal.Title = Title;
UIModal.Footer = Footer;

export default UIModal;
