import * as actions from './actions';

export const initialState = {
  loading: false,
  showForwardModal: false,
};

export const codeStep = (state = initialState, action) => {
  switch (action.type) {
    case actions.TOGGLE_SHOW_FORWARD_MODAL:
      return {
        ...state,
        showForwardModal: !state.showForwardModal,
      };
    case actions.SEND_EMAIL_REQUEST:
      return {
        ...state,
        loading: true,
      };
    case actions.SEND_EMAIL_SUCCESS:
      return {
        ...state,
        loading: false,
        showForwardModal: false,
      };
    case actions.SEND_EMAIL_FAILURE:
      return {
        ...state,
        loading: false,
        error: action.error,
      };
    default:
      return state;
  }
};
