import React from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';
import BuilderSteps from './BuilderSteps';
import * as actions from '../../../actions';
import I18n from '../../../shared/i18n';

const t = (key, params) => I18n.t(`lead_gen.embeddable_forms.subheader.${key}`, params);

class SubHeader extends React.PureComponent {
  static buildRoutes(embeddableId) {
    return ['templates', 'editar/aparencia', 'editar/conversao', 'editar/codigo'].map(
      path => (embeddableId ? `/${embeddableId}/${path}` : '#')
    );
  }

  static getDerivedStateFromProps(props, state) {
    return {
      ...state,
      routes: SubHeader.buildRoutes(props.embeddableId),
    };
  }

  constructor(props) {
    super(props);

    this.state = {
      routes: SubHeader.buildRoutes(props.embeddableId),
    };

    this.checkForUnsavedChanges = this.checkForUnsavedChanges.bind(this);
  }

  checkForUnsavedChanges(callback) {
    if (this.props.checkForUnsavedChangesAtCurrentStepMethod !== undefined) {
      if (this.props.checkForUnsavedChangesAtCurrentStepMethod()) {
        this.props.showDirtyModal(callback);
        return false;
      }
    }
    callback();
    return true;
  }

  buildSteps() {
    const disabled = !this.props.embeddableId;
    return [
      this.buildStep('templates', () => this.props.history.replace(this.state.routes[0]), !disabled),
      this.buildStep('editor', () => this.checkForUnsavedChanges(() => this.props.history.replace(this.state.routes[1])), disabled),
      this.buildStep('configuration', () => this.checkForUnsavedChanges(() => this.props.history.replace(this.state.routes[2])), disabled),
      this.buildStep('code', () => this.checkForUnsavedChanges(() => this.props.history.replace(this.state.routes[3])), disabled),
    ];
  }

  buildStep(label, clickFn, disabled) {
    return {
      label: t(label),
      onClick: clickFn,
      disabled,
    };
  }

  previousStepRoute() {
    return this.state.routes[this.props.activeStep - 1];
  }

  handleGoBackClick() {
    if (this.props.activeStep === 0 || this.props.activeStep === 1) {
      window.location.assign(window.Routes.landing_page_embeddables_path());
    } else {
      this.props.history.push(this.previousStepRoute());
    }
  }

  render() {
    return (
      <nav className="builder-subheader">
        <div className="steps-go-back">
          <button
            className="btn btn-default btn-sm go-rewind subheader-btn"
            onClick={() => this.checkForUnsavedChanges(this.handleGoBackClick.bind(this))}
          >
            {t('previous')}
          </button>
        </div>

        <div className="steps-subheader">
          <BuilderSteps
            activeStep={this.props.activeStep}
            steps={this.buildSteps()}
          />
        </div>

        <div className="actions-subheader">{this.props.children}</div>
      </nav>
    );
  }
}

SubHeader.propTypes = {
  history: PropTypes.shape({
    push: PropTypes.func.isRequired,
  }).isRequired,
  activeStep: PropTypes.number.isRequired,
  embeddableId: PropTypes.string,
  checkForUnsavedChangesAtCurrentStepMethod: PropTypes.func,
  children: PropTypes.node,
  showDirtyModal: PropTypes.func.isRequired,
};

SubHeader.defaultProps = {
  embeddableId: null,
  children: [],
  checkForUnsavedChangesAtCurrentStepMethod: undefined,
};

const mapStateToProps = (state) => {
  return {
    checkForUnsavedChangesAtCurrentStepMethod: state.embeddableApp.checkForUnsavedChangesAtCurrentStepMethod,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    showDirtyModal: (callbackForOriginalActionMethod) => {
      dispatch(actions.showDirtyModal(callbackForOriginalActionMethod));
    },
  };
};

export { SubHeader };
export default connect(mapStateToProps, mapDispatchToProps)(withRouter(SubHeader));
