import React from 'react';
import cs from 'classnames';
import PropTypes from 'prop-types';

/**
 * Group a series of buttons together on a single line with the button group.
 */

const UIButtons = props => {
  const { fluid, justified, vertical, size, className, ...rest } = props;

  const csResult = cs(
    'tangram buttons',
    className,
    { fluid },
    { justified },
    { vertical },
    size
  );

  return <div {...rest} className={csResult} />;
};

UIButtons.defaultProps = {
  fluid: false,
  justified: false,
  vertical: false,
};

UIButtons.propTypes = {
  className: PropTypes.string,
  /** Display fluid buttons; only useful when used with the "vertical" prop. */
  fluid: PropTypes.bool,
  /** Make a group of buttons stretch at equal sizes to span the entire width of its parent. */
  justified: PropTypes.bool,
  /** Change the size of all child buttons. */
  size: PropTypes.oneOf(['slim']),
  /** Put the child buttons on top of each other. */
  vertical: PropTypes.bool,
};

export default UIButtons;
