import * as actions from './actions';

export const initialState = {
  iframeLoaded: false,
  updating: false,
};

export const editorStep = (state = initialState, action) => {
  switch (action.type) {
    case actions.SET_IFRAME_LOADED:
      return {
        ...state,
        iframeLoaded: true,
      };
    case actions.SET_IFRAME_NOT_LOADED:
      return {
        ...state,
        iframeLoaded: false,
      };
    default:
      return state;
  }
};

