import React from 'react';
import PropTypes from 'prop-types';
import FormGroup from '../../../shared/components/lead-gen/common/FormGroup';
import I18n from '../../../shared/i18n';

const t = (key, params) => I18n.t(`lead_gen.embeddable_forms.code.forward.modal.form.${key}`, params);

const ToField = (
  {
    getFieldProps,
    getFieldError,
  }
) => {
  return (
    <FormGroup
      label={t('to')}
      errors={getFieldError('forwardEmail.to')}
    >
      <small>
        {` ${t('to_help')}`}
      </small>
      <input
        className="form-control"
        placeholder={t('to_placeholder')}
        {...getFieldProps('forwardEmail.to', {
          initialValue: '',
          rules: [
            {
              required: true,
              message: t('error.empty'),
            },
            {
              type: 'email',
              message: t('error.email'),
            },
          ],
        })}
      />
    </FormGroup>
  );
};

ToField.propTypes = {
  getFieldProps: PropTypes.func,
  getFieldError: PropTypes.func,
};

ToField.defaultProps = {
  getFieldProps: () => {},
  getFieldError: () => {},
};

export default ToField;
