import React from 'react';
import PropTypes from 'prop-types';
import cs from 'classnames';
import I18n from '../../../../shared/i18n';

const t = (key, params) => I18n.t(`lead_gen.embeddable_forms.conversion_tab.download_url.${key}`, params);

const DownloadFormActionInput = (
  {
    isActive,
    inputId,
    inputName,
    update,
    downloadURL,
    getFieldProps,
    getFieldError,
    setFieldsValue,
  }
) => {
  return (
    <div
      className={cs('', {
        'has-error': getFieldError('downloadURL'),
      })}
    >
      <div className="form-group input-group">
        <input
          className="form-control"
          id={inputId}
          maxLength="255"
          disabled={!isActive}
          size="255"
          type="text"
          name={inputName}
          {...getFieldProps('downloadURL', {
            initialValue: downloadURL,
            onChange: e => update('landingPageActions', 'downloadURL', e.target.value),
            rules: [
              {
                type: 'url',
                required: true,
                message: t('error.url'),
              },
            ],
          })}
        />
        <div className="input-group-btn" id="js-lp-tour-file-upload-text">
          <button
            type="button"
            onClick={() => window.RD.openUploadDialog((data) => {
              const unescapedURL = unescape(data.url);
              setFieldsValue({ downloadURL: unescapedURL });
              update('landingPageActions', 'downloadURL', unescapedURL);
            })}
            id="js-lp-tour-file-upload-link"
            className="file-upload-link btn btn-info"
          >
            {I18n.t('settings_tab.wizard_settings.form_actions.actions.download.upload_file')}
          </button>
        </div>
      </div>
      <small className="help-block">
        {getFieldError('downloadURL')}
      </small>
    </div>
  );
};

DownloadFormActionInput.propTypes = {
  isActive: PropTypes.bool,
  inputName: PropTypes.string.isRequired,
  inputId: PropTypes.string.isRequired,
  update: PropTypes.func,
  getFieldProps: PropTypes.func,
  getFieldError: PropTypes.func,
  setFieldsValue: PropTypes.func,
  downloadURL: PropTypes.string,
};

DownloadFormActionInput.defaultProps = {
  downloadURL: '',
  isActive: false,
  getFieldError: () => {},
  getFieldProps: () => {},
  setFieldsValue: () => {},
  update: () => {},
};

export default DownloadFormActionInput;
