export default class BrowserConfirmationBeforeUnload {
  static addConfirmation(conditionToShowConfirmation, message) {
    window.onbeforeunload = (event) => {
      if (conditionToShowConfirmation()) {
        if (event) { event.returnValue = message; }
        return message;
      }
    };
  }

  static removeConfirmation() {
    window.onbeforeunload = null;
  }
}
