import i18next from 'i18next';
import LanguageDetector from 'i18next-browser-languagedetector';
import locales from '../../locales';
import RDSMLocaleDetector from '../utils/RDSMLocaleDetector';

const detectionOptions = {
  order: [RDSMLocaleDetector.name, 'path', 'cookie', 'navigator'],
  lookupFromPathIndex: 0,
};
const resources = {};
locales.forEach(locale => {
  const key = Object.keys(locale)[0];
  resources[key] = { translation: locale[key] };
});
const languageDetector = new LanguageDetector();
languageDetector.addDetector(RDSMLocaleDetector);

i18next.use(languageDetector).init({
  fallbackLng: 'en',
  detection: detectionOptions,
  resources,
});

class I18n {
  static t(key, params) {
    return i18next.t(key, params);
  }
}

export default I18n;
