/* eslint-disable react/no-danger */
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import CopyToClipboard from '../../shared/utils/CopyToClipboard';
import { successMessage } from '../common/messages';
import { trackEvent } from '../common/events';
import I18n from '../../shared/i18n';

const t = (key, params) => I18n.t(`lead_gen.embeddable_forms.code.${key}`, params);

class EmbeddableCode extends Component {
  constructor(props) {
    super(props);
    this.copy = this.copy.bind(this);
  }

  componentDidMount() {
    const successCallback = () => successMessage(t('copied_successfully'));
    this.copyToClipboard = new CopyToClipboard(this.buttonRef, this.codeRef, successCallback);
  }

  setCodeRef(ref) {
    this.codeRef = ref;
  }

  setButtonRef(ref) {
    this.buttonRef = ref;
  }

  copy() {
    const { embeddableId } = this.props;
    trackEvent(
      'Code copied',
      { embeddable_id: embeddableId },
    );

    this.copyToClipboard.copyToClipboard();
  }

  renderButton() {
    return (
      <button
        ref={ref => this.setButtonRef(ref)}
        className="btn btn-default btn-sm btn-block"
        id="copy-code"
        type="button"
        onClick={this.copy}
      >
        {t('copy_button')}
      </button>
    );
  }

  renderHelp() {
    return (
      <React.Fragment>
        <h5><strong>{t('insert_yourself')}</strong></h5>
        <p className="help-block small">
          {t('insert_hint')}
          <a
            href={I18n.t('urls.lead_gen.embeddable_forms.code.help_link')}
            target="_blank"
            rel="noopener noreferrer"
            title={t('help_center')}
          >
            {t('know_more')}
          </a>
          .
        </p>
      </React.Fragment>
    );
  }

  renderCode() {
    const {
      codeSnippet,
    } = this.props;

    return (
      <React.Fragment>
        <div className="col-sm-3">
          {this.renderButton()}
        </div>
        <div className="col-sm-9">
          <pre id="embeddable_code" ref={ref => this.setCodeRef(ref)}>
            {codeSnippet}
          </pre>
        </div>
      </React.Fragment>
    );
  }

  render() {
    return (
      <React.Fragment>
        <div className="col-sm-3">
          <h5 className="">{t('code_title')}</h5>
        </div>
        <div className="col-sm-8">
          {this.renderHelp()}
          {this.renderCode()}
        </div>
      </React.Fragment>
    );
  }
}

EmbeddableCode.propTypes = {
  embeddableId: PropTypes.number.isRequired,
  codeSnippet: PropTypes.string.isRequired,
};

export default EmbeddableCode;
