import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import SubHeader from '../common/SubHeader';
import MainContent from './MainContent';
import { embeddableType } from '../../shapes';
import * as actions from '../../actions';
import * as codeActions from './actions';
import repository from '../../repositories/embeddables';
import ForwardModal from './ForwardModal';
import CodeSnippet from './CodeSnippet';
import { trackEvent } from '../common/events';
import I18n from '../../shared/i18n';

const t = (key, params) => I18n.t(`lead_gen.embeddable_forms.${key}`, params);

export class Code extends Component {
  constructor(props) {
    super(props);

    this.props.cleanDirtyModalConfig();
  }

  componentDidMount() {
    this.props.loadEmbeddable(this.props.embeddableId);
    repository.publishEmbeddable(this.props.embeddableId);
  }

  handlePreviewBtnClick() {
    const { embeddable, onPreview } = this.props;
    onPreview(embeddable);
  }

  renderSubHeader() {
    const { embeddableId } = this.props;
    return (
      <SubHeader activeStep={3} embeddableId={embeddableId}>
        <button
          type="button"
          className="btn btn-link btn-sm subheader-btn btn-preview"
          onClick={() => this.handlePreviewBtnClick()}
        >
          {t('preview')}
        </button>
        <a
          href={window.Routes.landing_page_embeddables_path()}
          className="btn btn-primary btn-sm go-forward subheader-btn save-and-forward"
          onClick={() => {
            trackEvent('Finish and exit',
              { embeddable_id: this.props.embeddableId }
            );
          }}
        >
          {t('finish_and_exit')}
        </a>
      </SubHeader>
    );
  }

  render() {
    const {
      embeddable,
      loading,
      toggleShowModal,
      showForwardModal,
      sendEmail,
    } = this.props;

    let codeSnippet = '';
    if (embeddable) {
      codeSnippet = new CodeSnippet(
        embeddable.url_slug,
        embeddable.analytics_ua_id,
        window.rdStaticCloudFront
      ).print();
    }
    return (
      <React.Fragment>
        {this.renderSubHeader()}
        <div className="container main-content">
          <MainContent
            embeddable={embeddable}
            loading={loading}
            toggleShowModal={toggleShowModal}
            codeSnippet={codeSnippet}
          />
        </div>
        <ForwardModal
          loading={loading}
          show={showForwardModal}
          onHide={toggleShowModal}
          onCreate={sendEmail}
          codeSnippet={codeSnippet}
          currentAccountDomain={window.currentAccountDomain}
          currentAccountName={window.currentAccountName}
        />
      </React.Fragment>
    );
  }
}

Code.propTypes = {
  embeddableId: PropTypes.string.isRequired,
  embeddable: embeddableType,
  loading: PropTypes.bool,
  loadEmbeddable: PropTypes.func.isRequired,
  toggleShowModal: PropTypes.func,
  sendEmail: PropTypes.func,
  showForwardModal: PropTypes.bool,
  cleanDirtyModalConfig: PropTypes.func.isRequired,
  onPreview: PropTypes.func,
};

Code.defaultProps = {
  embeddable: {},
  loading: false,
  toggleShowModal: () => {},
  sendEmail: () => {},
  showForwardModal: false,
  onPreview: () => {},
};

const mapStateToProps = (state) => {
  return {
    embeddable: state.embeddableApp.embeddable,
    loading: state.embeddableApp.loading,
    showForwardModal: state.codeStep.showForwardModal,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    loadEmbeddable: (id) => {
      dispatch(actions.loadEmbeddable(id));
    },
    toggleShowModal: (activeTemplateId) => {
      dispatch(codeActions.toggleShowModal(activeTemplateId));
    },
    sendEmail: (info) => {
      dispatch(codeActions.sendEmail(info));
    },
    cleanDirtyModalConfig: () => {
      dispatch(actions.cleanDirtyModalConfig());
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Code);
