/*
* actions
*/
import embeddablesRepository from '../../repositories/embeddables';

export const TOGGLE_SHOW_FORWARD_MODAL = 'TOGGLE_SHOW_FORWARD_MODAL';
export const SEND_EMAIL_REQUEST = 'SEND_EMAIL_REQUEST';
export const SEND_EMAIL_SUCCESS = 'SEND_EMAIL_SUCCESS';
export const SEND_EMAIL_FAILURE = 'SEND_EMAIL_FAILURE';

/*
* action creators
*/
export const toggleShowModal = () => ({
  type: TOGGLE_SHOW_FORWARD_MODAL,
});

export const sendEmailRequest = () => ({
  type: SEND_EMAIL_REQUEST,
});

export const sendEmailSuccess = () => ({
  type: SEND_EMAIL_SUCCESS,
});

export const sendEmailFailure = error => ({
  type: SEND_EMAIL_FAILURE,
  error,
});

export const sendEmail = info => (dispatch) => {
  dispatch(sendEmailRequest());

  return embeddablesRepository.sendEmail(info).then(
    () => {
      dispatch(sendEmailSuccess());
    },
    error => dispatch(sendEmailFailure(error))
  );
};
