import React, { Component } from 'react';
import { connect } from 'react-redux';
import { UIButton, UIModal } from '@resultadosdigitais/tangram-react';
import PropTypes from 'prop-types';
import * as actions from './actions';
import { successMessage, errorMessage } from '../messages';
import Form from './Form';
import embeddablesRepository from '../../../repositories/embeddables';
import I18n from '../../../shared/i18n';

const t = (key, params) => I18n.t(`lead_gen.embeddable_forms.${key}`, params);

export class Modal extends Component {
  constructor(props) {
    super(props);

    this.state = {
      title: props.initialTitle,
      embeddableId: props.embeddableId,
      loading: false,
    };
  }

  handleChange = (e) => {
    const newTitle = e.target.value;
    this.updateTitle(newTitle);
  };

  updateTitle = (newTitle) => {
    this.setState({ title: newTitle });
  }

  onSuccess = () => {
    const { title } = this.state;
    const { updateTitle } = this.props;
    updateTitle(title);
    this.setState({ loading: false });
    this.handleOnHide();
    successMessage(t('save_successfull'));
  }

  onError = () => {
    this.setState({ loading: false });
    this.handleOnHide();
    errorMessage(t('save_error'));
  }

  handleOnHide = () => {
    const { onHide } = this.props;
    if (onHide) {
      onHide('');
    }
  }

  updateContent = () => {
    this.setState({ loading: true });
    const { embeddableId, title } = this.state;
    embeddablesRepository.updateTitle(embeddableId, { landing_page_embeddable: { title } })
      .then(() => {
        this.onSuccess();
      }).catch(() => {
        this.onError();
      });
  }

  renderFooter(loading) {
    return (
      <UIModal.Footer>
        <UIButton priority="tertiary" className="cancel-email" onClick={this.handleOnHide}>
          {t('modal.actions.cancel')}
        </UIButton>
        <UIButton priority="primary" className="send-email" onClick={this.updateContent} loading={loading}>
          {t('modal.actions.save')}
        </UIButton>
      </UIModal.Footer>
    );
  }

  render() {
    const { show } = this.props;
    const { loading, title } = this.state;

    return (
      <UIModal show={show} onHide={this.handleOnHide} style={{ overflowY: 'scroll' }}>
        <UIModal.Header>
          {t('modal.header.title')}
        </UIModal.Header>
        <UIModal.Body>
          <Form
            title={title}
            handleChange={(e) => { this.handleChange(e); }}
          />
        </UIModal.Body>
        {this.renderFooter(loading)}
      </UIModal>
    );
  }
}

Modal.propTypes = {
  show: PropTypes.bool,
  onHide: PropTypes.func,
  updateTitle: PropTypes.func.isRequired,
  initialTitle: PropTypes.string.isRequired,
  embeddableId: PropTypes.number.isRequired,
};

Modal.defaultProps = {
  show: false,
  onHide: () => { },
};

const mapStateToProps = (state) => {
  return {
    title: state.embeddableApp.embeddable ? state.embeddableApp.embeddable.title : '',
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    updateTitle: (title) => {
      dispatch(actions.updateTitleSuccess(title));
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Modal);
