import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import * as actions from '../../../actions';
import DirtyModal from '../../../shared/utils/DirtyModal';
import BrowserConfirmationBeforeUnload from '../../../shared/utils/browser-confirmation-before-unload';
import I18n from '../../../shared/i18n';

const t = (key, params) => I18n.t(`lead_gen.embeddable_forms.warning_dialog.${key}`, params);

const WarningForUnsavedChanges = (props) => {
  const errorMessage = t('message');

  BrowserConfirmationBeforeUnload.addConfirmation(
    props.conditionToShowConfirmation,
    errorMessage,
  );

  return (
    <DirtyModal
      show={props.show}
      onClose={props.closeDirtyModal}
      onExitWithoutSave={props.onExitWithoutSave}
      onExitAndSave={props.onExitAndSave}
    >
      {errorMessage}
    </DirtyModal>
  );
};

WarningForUnsavedChanges.propTypes = {
  show: PropTypes.bool,
  onExitAndSave: PropTypes.func,
  onExitWithoutSave: PropTypes.func,
  conditionToShowConfirmation: PropTypes.func,
  closeDirtyModal: PropTypes.func.isRequired,
};

WarningForUnsavedChanges.defaultProps = {
  show: false,
  onExitAndSave: undefined,
  onExitWithoutSave: undefined,
  conditionToShowConfirmation: undefined,
};

const mapStateToProps = (state) => {
  return {
    show: state.embeddableApp.displayDirtyModal,
    conditionToShowConfirmation: state.embeddableApp.checkForUnsavedChangesAtCurrentStepMethod,
    onExitAndSave: () => {
      state.embeddableApp.onSaveForCurrentStepMethod();
      state.embeddableApp.callbackForOriginalActionMethod();
    },
    onExitWithoutSave: state.embeddableApp.callbackForOriginalActionMethod,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    closeDirtyModal: () => {
      dispatch(actions.closeDirtyModal());
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(WarningForUnsavedChanges);
