import React from 'react';
import PropTypes from 'prop-types';
import cs from 'classnames';

import Step from './Step';

const BuilderSteps = (props) => {
  const { steps, activeStep, className, ...rest } = props;
  return (
    <ul className={cs('steps steps-sm', className)} {...rest}>
      {steps.map(({ onClick, label, disabled }, index) => (
        <Step
          disabled={disabled}
          onClick={onClick}
          active={activeStep === index}
          key={label}
        >
          {label}
        </Step>
      ))}
    </ul>
  );
};

BuilderSteps.propTypes = {
  steps: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string,
      onClick: PropTypes.func,
    })
  ).isRequired,
  activeStep: PropTypes.number.isRequired,
  className: PropTypes.string,
};

BuilderSteps.defaultProps = {
  className: '',
};

export default BuilderSteps;
