import React from 'react';
import PropTypes from 'prop-types';
import { UIButton, UIModal } from '@resultadosdigitais/tangram-react';
import Form from './Form';
import I18n from '../../../shared/i18n';

const t = (key, params) => I18n.t(`lead_gen.embeddable_forms.code.forward.modal.${key}`, params);

class ForwardModal extends React.Component {
  constructor(props) {
    super(props);

    this.sendEmail = this.sendEmail.bind(this);
    this.handleOnHide = this.handleOnHide.bind(this);
  }

  setFormRef(ref) {
    this.formRef = ref;
  }

  getEmailBody() {
    const {
      codeSnippet,
      currentAccountDomain,
      currentAccountName,
    } = this.props;
    const emailBody = t('form.body_content', {
      account_domain: currentAccountDomain,
      account_name: currentAccountName,
      embeddable_code: codeSnippet,
      url_1: I18n.t('urls.lead_gen.embeddable_forms.code.forward.modal.form.url_1'),
    });
    return emailBody;
  }

  handleOnHide() {
    const { onHide } = this.props;
    if (onHide) {
      onHide('');
    }
  }

  sendEmail() {
    const { form } = this.formRef.props;

    form.validateFields((err, values) => {
      if (err) {
        return;
      }
      const { onCreate } = this.props;

      const emailInfo = {
        to_email: values.forwardEmail.to,
        subject: values.forwardEmail.subject,
        body: values.forwardEmail.body,
      };
      onCreate(emailInfo);
    });
  }

  render() {
    const {
      loading,
      show,
    } = this.props;

    return (
      <UIModal show={show} onHide={this.handleOnHide} style={{ overflowY: 'scroll' }}>
        <UIModal.Header>
          {t('title')}
        </UIModal.Header>
        <UIModal.Body>
          <Form
            wrappedComponentRef={formRef => this.setFormRef(formRef)}
            defaultEmailBody={this.getEmailBody()}
          />
        </UIModal.Body>
        <UIModal.Footer>
          <UIButton
            priority="tertiary"
            className="cancel-email"
            onClick={this.handleOnHide}
          >
            {t('cancel')}
          </UIButton>
          <UIButton
            priority="primary"
            className="send-email"
            onClick={this.sendEmail}
            loading={loading}
          >
            {t('submit')}
          </UIButton>
        </UIModal.Footer>
      </UIModal>
    );
  }
}

ForwardModal.propTypes = {
  codeSnippet: PropTypes.string,
  currentAccountDomain: PropTypes.string,
  currentAccountName: PropTypes.string,
  loading: PropTypes.bool,
  show: PropTypes.bool,
  onHide: PropTypes.func,
  onCreate: PropTypes.func,
};

ForwardModal.defaultProps = {
  codeSnippet: '',
  currentAccountDomain: '',
  currentAccountName: '',
  loading: false,
  show: false,
  onHide: () => {},
  onCreate: () => {},
};

export default ForwardModal;
