import thunkMiddleware from 'redux-thunk';
import { applyMiddleware, combineReducers, createStore } from 'redux';
import { composeWithDevTools } from 'redux-devtools-extension/developmentOnly';
import { embeddableApp } from './reducer';
import { templatesStep } from './components/Templates/reducer';
import { editorStep } from './components/Editor/reducer';
import { configurationStep } from './components/Configuration/reducer';
import { codeStep } from './components/Code/reducer';
import { updateTitleReducer } from './components/common/Modal/reducer';

const rootReducer = combineReducers({
  embeddableApp,
  templatesStep,
  editorStep,
  configurationStep,
  codeStep,
  updateTitleReducer,
});

const store = createStore(
  rootReducer,
  composeWithDevTools(applyMiddleware(thunkMiddleware))
);

export default store;
