import PropTypes from 'prop-types';
import React from 'react';
import {
  Modal,
  Button,
  FormControl,
  FormGroup,
  ControlLabel,
  HelpBlock,
} from 'react-bootstrap';

import LoadingButton from '../../react/LoadingButton';
import validateSenderEmail from '../../whitelabel/validateSenderEmail';
import isBlank from '../../string-helper';
import I18n from '../../../i18n';

const t = (key, params) => I18n.t(`double_opt_in.components.modal.${key}`, params);

class DoubleOptInModal extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      name: props.name,
      email: props.email,
      language: props.language,
      pageDisplayName: props.pageDisplayName,
    };

    this.handleChange = this.handleChange.bind(this);
    this.save = this.save.bind(this);
  }

  setEmailError(error) {
    this.setState({ emailError: error });
  }

  setLoading(loadingState) {
    this.setState({ isLoading: loadingState });
  }

  clearEmailErrors() {
    this.setState({ emailError: null });
  }

  handleChange(event) {
    this.setState({ [event.target.name]: event.target.value });
    if (this.props.update) {
      this.props.update('doubleOptIn', event.target.name, event.target.value);
    }
  }

  saveSubmittedEmailDomain() {
    this.setState({
      submittedEmailDomain: this.state.email.replace(/.*@/, ''),
    });
  }

  runSaveCallback() {
    this.props.onSave({
      name: this.state.name,
      email: this.state.email,
      language: this.state.language,
      pageDisplayName: this.state.pageDisplayName,
    });
  }

  save(event) {
    event.preventDefault();
    this.saveSubmittedEmailDomain();
    this.setLoading(true);
    validateSenderEmail(this.state.email).then(({ valid, error }) => {
      if (valid) {
        this.clearEmailErrors();
        this.runSaveCallback();
      } else {
        this.setEmailError(error);
      }

      this.setLoading(false);
    });
  }

  buttonDisabled() {
    return isBlank(this.state.name) ||
           isBlank(this.state.email) ||
           isBlank(this.state.pageDisplayName);
  }

  render() {
    const {
      isLoading,
      name,
      emailError,
      email,
      pageDisplayName,
      language,
      submittedEmailDomain,
    } = this.state;

    return (
      <Modal
        id="double-opt-in-modal"
        show={this.props.show}
        onHide={this.props.onClose}
      >
        <Modal.Header closeButton>
          <Modal.Title>{t('title')}</Modal.Title>
        </Modal.Header>

        <form
          id="double-opt-in-configuration-modal-form"
          className="fix-missing-icon"
          onSubmit={this.save}
        >
          <Modal.Body>
            <FormGroup>
              <ControlLabel className="mb-0">{t('sender_name')}</ControlLabel>
              <small>
                <HelpBlock className="mt-0">
                  {t('sender_name_help_text')}
                </HelpBlock>
              </small>
              <FormControl
                type="text"
                name="name"
                placeholder={t('sender_name_placeholder')}
                id="sender-name"
                onChange={this.handleChange}
                value={name}
              />
              <FormControl.Feedback />
            </FormGroup>

            <FormGroup
              className={emailError && 'field field_with_errors'}
            >
              <ControlLabel>{t('sender_email')}</ControlLabel>
              <FormControl
                type="text"
                name="email"
                id="sender-email"
                onChange={this.handleChange}
                value={email}
                placeholder={t('sender_email_placeholder')}
              />
              <FormControl.Feedback />
              {emailError &&
                <p
                  className="text-danger"
                  id="email-error-message"
                  dangerouslySetInnerHTML={{ __html: t(`sender_email_errors.${emailError}`, { domain: submittedEmailDomain }) }}
                />
              }
            </FormGroup>

            <div id="msg-alert" className="message message-info">
              <p dangerouslySetInnerHTML={{ __html: t('sender_email_info_message') }} />
            </div>

            <hr />

            <FormGroup>
              <ControlLabel className="mb-0">{t(`${this.props.formBaseType}.page_display_name`)}</ControlLabel>
              <small>
                <HelpBlock className="mt-0">
                  {t(`${this.props.formBaseType}.page_display_name_help_text`)}
                </HelpBlock>
              </small>
              <FormControl
                type="text"
                name="pageDisplayName"
                id="page-display-name"
                onChange={this.handleChange}
                value={pageDisplayName}
              />
              <FormControl.Feedback />
            </FormGroup>

            <FormGroup>
              <ControlLabel className="mb-0">{t('language')}</ControlLabel>
              <small>
                <HelpBlock className="mt-0" id="language-help-text">
                  {t(`${this.props.formBaseType}.language_help_text`)}
                </HelpBlock>
              </small>
              <FormControl
                componentClass="select"
                type="text"
                name="language"
                id="sender-language"
                value={language}
                onChange={this.handleChange}
              >
                <option value="pt-BR">{t('languages.portuguese')}</option>
                <option value="en">{t('languages.english')}</option>
                <option value="es">{t('languages.spanish')}</option>
              </FormControl>
            </FormGroup>

          </Modal.Body>
          <Modal.Footer>
            <Button
              id="double-opt-in-modal-cancel-btn"
              bsStyle="link"
              onClick={this.props.onClose}
            >
              {t('cancel_button')}
            </Button>
            <LoadingButton
              id="double-opt-in-configuration-modal-save-button"
              bsStyle="primary"
              disabled={this.buttonDisabled()}
              onClick={this.save}
              isLoading={isLoading}
              type="submit"
            >
              {t('save_button')}
            </LoadingButton>
          </Modal.Footer>
        </form>
      </Modal>
    );
  }
}

DoubleOptInModal.propTypes = {
  name: PropTypes.string,
  email: PropTypes.string,
  language: PropTypes.string,
  pageDisplayName: PropTypes.string.isRequired,
  show: PropTypes.bool,
  onSave: PropTypes.func.isRequired,
  onClose: PropTypes.func.isRequired,
  formBaseType: PropTypes.string.isRequired,
  update: PropTypes.func,
};

DoubleOptInModal.defaultProps = {
  name: '',
  email: '',
  language: '',
  show: false,
  update: () => {},
};

export default DoubleOptInModal;
