import React from 'react';
import PropTypes from 'prop-types';
import {
  FormGroup,
  HelpBlock,
  Checkbox,
  Collapse,
} from 'react-bootstrap';

import LoadingSpinner from '../../rds-loading-spinner';
import DoubleOptInModal from './double-opt-in-modal';
import DoubleOptInHelpTooltip from './double-opt-in-help-tooltip';
import validateWhitelabel from '../../whitelabel/validateWhitelabel';
import I18n from '../../../i18n';

const t = (key, params) => I18n.t(`double_opt_in.components.configuration_section.${key}`, params);

class DoubleOptInConfigurationSection extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      active: props.active,
      name: props.name,
      email: props.email,
      language: props.language,
      pageDisplayName: props.pageDisplayName,
      isOpen: false,
      hasWarning: false,
      whitelabelWarningCode: '',
      messageBoxesLoading: props.active,
    };

    this.openModal = this.openModal.bind(this);
    this.closeModal = this.closeModal.bind(this);
    this.toggleCheckbox = this.toggleCheckbox.bind(this);
    this.save = this.save.bind(this);
  }

  componentDidMount() {
    if (this.state.active) {
      this.checkWhitelabel();
    }
  }

  componentDidUpdate(_prevProps, prevState) {
    const doubleOpInWasActivated = !prevState.active && this.state.active;
    const emailWasChanged = this.state.email !== prevState.email;

    if (doubleOpInWasActivated || emailWasChanged) {
      this.checkWhitelabel();
    }
  }

  setMessageBoxesLoading(value) {
    this.setState({ messageBoxesLoading: value });
  }

  shouldShowMessageBoxesLoading() {
    return this.state.messageBoxesLoading;
  }

  checkWhitelabel() {
    this.setMessageBoxesLoading(true);
    validateWhitelabel(this.emailDomain()).then((result) => {
      this.setState({
        hasWarning: result.data.valid !== 'success',
        whitelabelWarningCode: result.data.valid,
        messageBoxesLoading: false,
      });
    });
  }

  toggleCheckbox() {
    if (!this.hasConfiguration()) {
      this.openModal();
    }
    if (this.props.update) {
      this.props.update('doubleOptIn', 'active', !this.state.active);
    }
    this.setState({ active: !this.state.active });
  }

  openModal() {
    this.setState({ isOpen: true });
  }

  closeModal() {
    this.setState({ active: this.hasConfiguration(), isOpen: false });
    if (this.props.update) {
      this.props.update('doubleOptIn', 'active', this.hasConfiguration());
    }
  }

  save({ name, email, language, pageDisplayName }) {
    this.setState({ name, email, language, pageDisplayName }, () => {
      this.closeModal();
    });
  }

  hasConfiguration() {
    return Boolean(this.state.name) && Boolean(this.state.email);
  }

  emailDomain() {
    return this.state.email.replace(/.*@/, '');
  }

  pageDisplayNameMessage() {
    const message = t(`${this.props.formBaseType}.page_display_name_message`);
    const pageDisplayName = (
      <a onClick={this.openModal} className="btn-link" id="page-display-name-link" href="#open">
        {this.state.pageDisplayName}.
      </a>
    );

    return (
      <span>
        {message}{' '}
        {pageDisplayName}{' '}
        <DoubleOptInHelpTooltip formBaseType={this.props.formBaseType} />
      </span>
    );
  }

  emailMessage() {
    const message = t('configuration_message_email');
    const email = (
      <a onClick={this.openModal} className="btn-link" id="email-link" href="#open">
        {this.state.email}.
      </a>
    );

    return (
      <span>{message} {email}</span>
    );
  }

  languageMessage() {
    const message = t('configuration_message_language');
    const language = (
      <a onClick={this.openModal} className="btn-link" id="language-link" href="#open">
        {I18n.t(`configuration.accounts.language.${this.state.language}`)}.
      </a>
    );

    return (
      <span>{message} {language}</span>
    );
  }

  whiteLabelWarning() {
    const i18nKey = `whitelabel.${this.state.whitelabelWarningCode}`;
    const data = { domain: this.emailDomain(), link: window.Routes.domain_configurations_path() };

    return (
      <div className="message message-warning">
        <p>
          <strong>
            {t('domain_warning_title')}
          </strong>
        </p>
        <p dangerouslySetInnerHTML={{ __html: t(i18nKey, data) }} />
      </div>
    );
  }

  helpBlockMessage() {
    const description = t(`${this.props.formBaseType}.help_block_description`);

    const link = (<a
      id="double-opt-in-help-link"
      target="_blank"
      rel="noopener noreferrer"
      href={I18n.t('urls.double_opt_in.components.configuration_section.help_link')}
    >
      {t('help_link_text')}
    </a>);

    return (<span>{description}{' '}{link}</span>);
  }

  renderConfigurationSummary() {
    return (
      <React.Fragment>
        {this.pageDisplayNameMessage()}<br />
        {this.emailMessage()}<br />
        {this.languageMessage()}
      </React.Fragment>
    );
  }

  renderMessageBoxes() {
    return (
      <Collapse in={this.hasConfiguration() && this.state.active}>
        <div id="msg-alert">
          <div className="message message-info">
            <div className="configuration-summary">
              {this.renderConfigurationSummary()}
            </div>
            <div className="double-opt-in-edit-button">
              <a
                onClick={this.openModal}
                className="btn btn-info"
                id="double-opt-in-edit-link"
                href="#edit"
              >
                {t('edit')}
              </a>
            </div>
          </div>
          {this.state.hasWarning && this.whiteLabelWarning()}
        </div>
      </Collapse>
    );
  }

  render() {
    const { isOpen, active, name, email, language, pageDisplayName } = this.state;

    return (
      <div>
        <FormGroup>
          <Checkbox
            id="double-opt-in-checkbox"
            checked={active}
            value={active}
            onChange={this.toggleCheckbox}
            name="[double_opt_in_configuration][active]"
          >
            {t('checkbox_title')}
            <small>
              <HelpBlock>
                {this.helpBlockMessage()}
              </HelpBlock>
            </small>
          </Checkbox>
          {this.shouldShowMessageBoxesLoading() &&
            <div>
              <LoadingSpinner className="center-block" width={30} />
            </div>
          }
          {!this.shouldShowMessageBoxesLoading() && this.renderMessageBoxes()}
        </FormGroup>
        <DoubleOptInModal
          name={this.props.name}
          email={this.props.email}
          language={this.props.language}
          pageDisplayName={this.props.pageDisplayName}
          show={isOpen}
          onSave={this.save}
          onClose={this.closeModal}
          formBaseType={this.props.formBaseType}
          update={this.props.update}
        />
        <input
          type="hidden"
          id="sender-name"
          name="[double_opt_in_configuration][name]"
          value={name}
        />
        <input
          type="hidden"
          id="sender-email"
          name="[double_opt_in_configuration][email]"
          value={email}
        />
        <input
          type="hidden"
          id="page-display-name"
          name="[double_opt_in_configuration][page_display_name]"
          value={pageDisplayName}
        />
        <input
          type="hidden"
          id="sender-language"
          name="[double_opt_in_configuration][language]"
          value={language}
        />
      </div>
    );
  }
}

DoubleOptInConfigurationSection.propTypes = {
  active: PropTypes.bool.isRequired,
  name: PropTypes.string,
  email: PropTypes.string,
  language: PropTypes.string.isRequired,
  pageDisplayName: PropTypes.string.isRequired,
  formBaseType: PropTypes.string.isRequired,
  update: PropTypes.func,
};

DoubleOptInConfigurationSection.defaultProps = {
  name: '',
  email: '',
  update: () => {},
};

export default DoubleOptInConfigurationSection;
