import React from 'react';
import PropTypes from 'prop-types';
import FormGroup from '../../../shared/components/lead-gen/common/FormGroup';
import I18n from '../../../shared/i18n';

const t = (key, params) => I18n.t(`lead_gen.embeddable_forms.code.forward.modal.form.${key}`, params);

const BodyField = (
  {
    getFieldProps,
    getFieldError,
    defaultValue,
  }
) => {
  return (
    <FormGroup
      label={t('body')}
      help={t('body_help')}
      errors={getFieldError('forwardEmail.body')}
    >
      <textarea
        rows="10"
        className="form-control"
        {...getFieldProps('forwardEmail.body', {
          initialValue: defaultValue,
          rules: [
            {
              required: true,
              message: t('error.empty'),
            },
          ],
        })}
      />
    </FormGroup>
  );
};

BodyField.propTypes = {
  getFieldProps: PropTypes.func,
  getFieldError: PropTypes.func,
  defaultValue: PropTypes.string,
};

BodyField.defaultProps = {
  getFieldProps: () => {},
  getFieldError: () => {},
  defaultValue: '',
};

export default BodyField;
