/* eslint-disable react/no-danger */
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import cs from 'classnames';
import CenteredSpinner from '../common/CenteredSpinner';
import TemplatesList from './TemplatesList';
import { templateType } from '../../shapes';
import { trackEvent } from '../common/events';
import I18n from '../../shared/i18n';

const t = (key, params) => I18n.t(`lead_gen.embeddable_forms.templates.${key}`, params);

class MainContent extends Component {
  renderTemplateList() {
    const {
      loading,
      templates,
    } = this.props;
    return (
      <TemplatesList
        loading={loading}
        templates={templates}
        card={template => (
          <TemplatesList.Card
            title={t(template.i18n_key)}
            image={{
              url: template.view_slug,
              alt: t(template.i18n_key),
            }}
            description={(
              <TemplatesList.Card.Author
                name={template.author_name}
                country={template.author_country}
              />
)}
            actions={
            this.renderActions(template.id)
          }
            labels={<span className="label label-beta">{t('labels.new_editor')}</span>}
          />
        )}
      />
    );
  }

  renderActions(templateId) {
    return (
      <div>
        <button
          type="button"
          tabIndex="0"
          className="btn btn-default view-template"
          onClick={() => {
            trackEvent('Templates Preview', { template_id: templateId });
            window.open(`/formularios/templates/${templateId}`, '_newtab');
          }}
        >
          {t('actions.view')}
        </button>
        <button
          type="button"
          tabIndex="0"
          className="btn btn-primary create-embeddable"
          onClick={() => {
            trackEvent('Select Template', { template_id: templateId });
            this.props.toggleShowModal(`${templateId}`);
          }}
        >
          {t('actions.select')}
        </button>
      </div>
    );
  }

  render() {
    const {
      loading,
    } = this.props;

    return (
      <section
        className={cs('container-fluid main-content', {
          'loading-overlay': loading,
        })}
      >
        {loading ? (
          <CenteredSpinner />
        ) : (
          <div className="row row-gutter-sm">
            {this.renderTemplateList()}
          </div>
        )}
      </section>
    );
  }
}

MainContent.propTypes = {
  toggleShowModal: PropTypes.func,
  loading: PropTypes.bool,
  templates: PropTypes.arrayOf(templateType),
};

MainContent.defaultProps = {
  toggleShowModal: () => {},
  loading: false,
  templates: [],
};

export default MainContent;
