import * as actions from './actions';

export const initialState = {
  loading: false,
  templates: [],
  showModal: false,
  activeTemplateId: '',
};

export const templatesStep = (state = initialState, action) => {
  switch (action.type) {
    case actions.LOAD_TEMPLATES_REQUEST:
      return {
        ...state,
        loading: true,
      };
    case actions.LOAD_TEMPLATES_FAILURE:
      return {
        ...state,
        loading: false,
        error: action.error,
      };
    case actions.LOAD_TEMPLATES_SUCCESS:
      return {
        ...state,
        loading: false,
        templates: action.templates,
      };
    case actions.TOGGLE_SHOW_MODAL:
      return {
        ...state,
        activeTemplateId: action.activeTemplateId,
        showModal: !state.showModal,
      };
    case actions.CREATE_EMBEDDABLE_REQUEST:
      return {
        ...state,
        loading: true,
        embeddableId: action.embeddableId,
      };
    case actions.CREATE_EMBEDDABLE_SUCCESS:
      return {
        ...state,
        loading: false,
        showModal: false,
        embeddableId: action.embeddableId,
      };
    case actions.CREATE_EMBEDDABLE_FAILURE:
      return {
        ...state,
        loading: false,
        error: action.error,
      };
    default:
      return state;
  }
};

