/* eslint-disable react/no-danger */
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import cs from 'classnames';
import { embeddableType } from '../../shapes';
import CenteredSpinner from '../common/CenteredSpinner';

class MainContent extends Component {

  componentDidUpdate() {
    if (!this.props.loading && this.props.iframeLoaded) {
      this.setBricksPayload(this.props.embeddable.payload);
    }
  }

  componentWillUnmount() {
    this.props.setIframeNotLoaded();
  }

  setBricksPayload(payload) {
    this.editor.contentWindow.editor.setDesign(payload);
  }

  renderBricksIframe(route) {
    return (
      <iframe
        ref={ref => (this.editor = ref)}
        id="bricks-editor"
        src={route}
        width="100%"
        frameBorder="0"
        className="preview-window"
        onLoad={this.props.setIframeLoaded}
        style={{
          position: 'fixed',
          top: 110,
          left: 0,
          height: 'calc(100% - 109px)',
          overflow: 'hidden',
        }}
      />
    );
  }

  render() {
    const {
      embeddable,
      loading,
      iframeLoaded,
    } = this.props;
    const editorPath = window.Routes.embeddable_forms_editor_path();

    return (
      <section
        id="content"
        className={cs('content-builder  has-subheader', {
          'loading-overlay': loading,
        })}
      >
        <div className="col-md-12 col-lg-12">
          {(loading || !iframeLoaded) && (<CenteredSpinner />)}
          {embeddable && this.renderBricksIframe(editorPath)}
        </div>
      </section>
    );
  }
}

MainContent.propTypes = {
  embeddable: embeddableType,
  loading: PropTypes.bool,
  iframeLoaded: PropTypes.bool,
  setIframeLoaded: PropTypes.func.isRequired,
  setIframeNotLoaded: PropTypes.func.isRequired,
};

MainContent.defaultProps = {
  embeddable: null,
  loading: false,
  iframeLoaded: false,
};

export default MainContent;
