import * as actions from './actions';

export const initialState = {
  title: '',
  loading: false,
  showModal: false,
};

export const updateTitleReducer = (state = initialState, action) => {
  switch (action.type) {
    case actions.UPDATE_TITLE_SUCCESS:
      return {
        ...state,
        title: action.title,
      };
    default:
      return state;
  }
};
