import React from 'react';
import PropTypes from 'prop-types';
import ReactDOM from 'react-dom';
import cs from 'classnames';
import events from 'dom-helpers/events';
import ownerDocument from 'dom-helpers/ownerDocument';
import canUseDOM from 'dom-helpers/util/inDOM';
import getScrollbarSize from 'dom-helpers/util/scrollbarSize';
import BaseModal from 'react-overlays/lib/Modal';
import isOverflowing from 'react-overlays/lib/utils/isOverflowing';
import Fade from 'react-bootstrap/lib/Fade';
import createChainedFunction from 'react-bootstrap/lib/utils/createChainedFunction';
import splitComponentProps from 'react-bootstrap/lib/utils/splitComponentProps';

import Box from './Box';

/* eslint-disable no-use-before-define, react/no-multi-comp */
function DialogTransition(props) {
  return <Fade {...props} timeout={Modal.TRANSITION_DURATION} />;
}

function BackdropTransition(props) {
  return <Fade {...props} timeout={Modal.BACKDROP_TRANSITION_DURATION} />;
}

/* eslint-enable no-use-before-define */

class Modal extends React.Component {
  constructor(props, context) {
    super(props, context);

    this.handleEntering = this.handleEntering.bind(this);
    this.handleExited = this.handleExited.bind(this);
    this.handleWindowResize = this.handleWindowResize.bind(this);
    this.handleDialogClick = this.handleDialogClick.bind(this);
    this.setModalRef = this.setModalRef.bind(this);

    this.state = {
      style: {},
    };
  }

  getChildContext() {
    return {
      $ui_modal: {
        onHide: this.props.onHide,
      },
    };
  }

  componentWillUnmount() {
    // Clean up the listener if we need to.
    this.handleExited();
  }

  setModalRef(ref) {
    this._modal = ref;
  }

  handleDialogClick(e) {
    if (e.target !== e.currentTarget) {
      return;
    }

    this.props.onHide();
  }

  handleEntering() {
    events.on(window, 'resize', this.handleWindowResize);
    this.updateStyle();
  }

  handleExited() {
    events.off(window, 'resize', this.handleWindowResize);
  }

  handleWindowResize() {
    this.updateStyle();
  }

  updateStyle() {
    if (!canUseDOM) {
      return;
    }

    const dialogNode = this._modal.getDialogElement();
    const dialogHeight = dialogNode.scrollHeight;

    const document = ownerDocument(dialogNode);
    const bodyIsOverflowing = isOverflowing(
      ReactDOM.findDOMNode(this.props.container || document.body)
    );
    const modalIsOverflowing =
      dialogHeight > document.documentElement.clientHeight;

    this.setState({
      style: {
        paddingRight:
          bodyIsOverflowing && !modalIsOverflowing
            ? getScrollbarSize()
            : undefined,
        paddingLeft:
          !bodyIsOverflowing && modalIsOverflowing
            ? getScrollbarSize()
            : undefined,
      },
    });
  }

  render() {
    const {
      animation,
      show,
      className,
      style,
      children, // Just in case this get added to BaseModal propTypes.
      onEntering,
      onExited,
      ...props
    } = this.props;

    const [baseModalProps, modalProps] = splitComponentProps(props, BaseModal);
    const inClassName = show && !animation && 'in';

    const backdropClasses = cs('modal-backdrop', inClassName);
    const modalClasses = cs(className, inClassName);

    return (
      <BaseModal
        {...baseModalProps}
        ref={this.setModalRef}
        show={show}
        containerClassName="modal-open"
        transition={DialogTransition}
        backdrop="static"
        backdropTransition={BackdropTransition}
        backdropClassName={backdropClasses}
        onEntering={createChainedFunction(onEntering, this.handleEntering)}
        onExited={createChainedFunction(onExited, this.handleExited)}
      >
        <Box
          {...modalProps}
          style={{ ...this.state.style, ...style }}
          className={modalClasses}
          onClick={null}
        >
          {children}
        </Box>
      </BaseModal>
    );
  }
}

Modal.propTypes = {
  /**
   * Open and close the Modal with a slide and fade animation.
   */
  animation: PropTypes.bool,

  /**
   * When `true` The modal will show itself.
   */
  show: PropTypes.bool,

  /** Size of the modal */
  size: PropTypes.oneOf(['large']),

  /**
   * A callback fired when the header closeButton or non-static backdrop is
   * clicked. Required if either are specified.
   */
  onHide: PropTypes.func,

  /**
   * Callback fired before the Modal transitions in
   */
  onEnter: PropTypes.func,

  /**
   * Callback fired as the Modal begins to transition in
   */
  onEntering: PropTypes.func,

  /**
   * Callback fired after the Modal finishes transitioning in
   */
  onEntered: PropTypes.func,

  /**
   * Callback fired right before the Modal transitions out
   */
  onExit: PropTypes.func,

  /**
   * Callback fired as the Modal begins to transition out
   */
  onExiting: PropTypes.func,

  /**
   * Callback fired after the Modal finishes transitioning out
   */
  onExited: PropTypes.func,

  /** Additional classes to be passed to .dialog element */
  className: PropTypes.string,

  /**
   * Additional classes to be passed to .box element
   */
  boxClassName: PropTypes.string,
};
Modal.defaultProps = {
  ...BaseModal.defaultProps,
  animation: true,
  show: true,
};

Modal.childContextTypes = {
  $ui_modal: PropTypes.shape({
    onHide: PropTypes.func,
  }),
};


Modal.TRANSITION_DURATION = 300;
Modal.BACKDROP_TRANSITION_DURATION = 150;

export default Modal;
