import cs from 'classnames';
import PropTypes from 'prop-types';
import React from 'react';
import CloseButton from 'react-bootstrap/lib/CloseButton';
import createChainedFunction from 'react-bootstrap/lib/utils/createChainedFunction';

import UIModalTitle from './UIModalTitle';
import I18n from '../utils/I18n';
import translation from './translation';

/**
 * It renders the header portion of a Modal or Dialog component
 */

const UIModalHeader = (props, context) => {
  const i18n = new I18n(translation);

  const { onHide, className, children, ...rest } = props;
  const modal = context.$ui_modal;
  const csResult = cs('header', className);

  return (
    <div {...rest} className={csResult}>
      <CloseButton
        label={i18n.t('close')}
        onClick={createChainedFunction(modal && modal.onHide, onHide)}
      />
      <UIModalTitle>{children}</UIModalTitle>
    </div>
  );
};

UIModalHeader.propTypes = {
  /**
   * A Callback fired when the close button is clicked. If used directly inside
   * a Modal component, the onHide will automatically be propagated up to the
   * parent Modal `onHide`.
   */
  onHide: PropTypes.func,
  /** Additional classes */
  className: PropTypes.string,
};

UIModalHeader.contextTypes = {
  $ui_modal: PropTypes.shape({
    onHide: PropTypes.func,
  }),
};

export default UIModalHeader;
