/*
* actions
*/
export const SET_IFRAME_LOADED = 'SET_IFRAME_LOADED';
export const SET_IFRAME_NOT_LOADED = 'SET_IFRAME_NOT_LOADED';

/*
* action creators
*/

export const setIframeLoaded = () => ({
  type: SET_IFRAME_LOADED,
});

export const setIframeNotLoaded = () => ({
  type: SET_IFRAME_NOT_LOADED,
});
