import React from 'react';
import cs from 'classnames';
import PropTypes from 'prop-types';

const UIDialogMessage = props => {
  const { className, ...rest } = props;

  const classes = cs('dialog-message', className);

  return <div {...rest} className={classes} />;
};

UIDialogMessage.propTypes = {
  /** Additional classes to be passed to the element */
  className: PropTypes.string,
};

export default UIDialogMessage;
