import axios from '../RDSMAxiosClient.js';

const errorCodesMap = {
  sender_forbidden_domain: 'forbidden_domain',
  sender_email_address: 'invalid_format',
};

function normalizeResponse({ valid, errors }) {
  if (valid) {
    return { valid };
  }

  return { valid, error: errorCodesMap[errors[0]] };
}

export default function validateFormatAndDomain(email) {
  return new Promise((resolve) => {
    axios
      .get(window.Routes.validate_sender_email_path(), { params: { email } })
      .then(response => resolve(normalizeResponse(response.data)));
  });
}
