import FlashMessage from '../../shared/utils/flash-message';

export const successMessage = (message) => {
  FlashMessage.now(message, {
    type: 'success',
    width: 460,
  });
};

export const errorMessage = (message) => {
  FlashMessage.now(message, {
    type: 'danger',
    width: 460,
  });
};
