import React from 'react';
import PropTypes from 'prop-types';
import { UIButton, UIModal } from '@resultadosdigitais/tangram-react';
import Form from './Form';
import I18n from '../../../shared/i18n';

const t = (key, params) => I18n.t(`lead_gen.embeddable_forms.templates.modal.${key}`, params);

class CreatingModal extends React.Component {
  constructor(props) {
    super(props);

    this.submitEmbeddable = this.submitEmbeddable.bind(this);
    this.handleOnHide = this.handleOnHide.bind(this);
  }

  setFormRef(ref) {
    this.formRef = ref;
  }

  submitEmbeddable() {
    const { form } = this.formRef.props;

    form.validateFields((err, values) => {
      if (err) {
        return;
      }

      this.props.onCreate(values);
    });
  }

  handleOnHide() {
    if (this.props.onHide) {
      this.props.onHide('');
    }
  }

  render() {
    const { show } = this.props;

    return (
      <UIModal show={show} onHide={this.handleOnHide}>
        <UIModal.Header>
          {t('title')}
        </UIModal.Header>
        <UIModal.Body>
          <Form
            submitEmbeddable={this.submitEmbeddable}
            wrappedComponentRef={formRef => this.setFormRef(formRef)}
            activeTemplateId={this.props.activeTemplateId}
          />
        </UIModal.Body>
        <UIModal.Footer>
          <UIButton
            priority="tertiary"
            className="cancel-embeddable"
            onClick={this.handleOnHide}
          >
            {t('cancel')}
          </UIButton>
          <UIButton
            type="submit"
            priority="primary"
            className="submit-embeddable"
            onClick={this.submitEmbeddable}
            loading={this.props.loading}
          >
            {t('submit')}
          </UIButton>
        </UIModal.Footer>
      </UIModal>
    );
  }
}

CreatingModal.propTypes = {
  activeTemplateId: PropTypes.string,
  loading: PropTypes.bool,
  show: PropTypes.bool,
  onHide: PropTypes.func,
  onCreate: PropTypes.func,
};

CreatingModal.defaultProps = {
  activeTemplateId: '',
  loading: false,
  show: false,
  onHide: () => {},
  onCreate: () => {},
};

export default CreatingModal;
