import React, { Component } from 'react';
import { createForm, formShape } from 'rc-form';
import PropTypes from 'prop-types';
import { withFeatureFlagsMapping } from '../../../shared/utils/feature-flagger/react';
import FormActions from './FormActions';
import FeedbackMessage from './FeedbackMessage';
import FeedbackEmail from './FeedbackEmail';
import EmailNotification from './EmailNotification';
import EmbeddableTitle from './EmbeddableTitle';
import DoubleOptIn from './DoubleOptIn';
import AntiBot from './AntiBot';
import { embeddableType } from '../../../shapes';

class Form extends Component {
  renderDoubleOptIn() {
    const { doubleOptIn, update } = this.props;

    if (!window.isCdpReadRolloutReleased) {
      return (
        <div>
          <hr className="spacer-md" />
          <DoubleOptIn doubleOptIn={doubleOptIn} update={update} />
        </div>
      );
    }

    return (<div />);
  }

  renderAntiBot() {
    const { captchaStatus, update, isGoogleRecaptchaReleased } = this.props;
    if (isGoogleRecaptchaReleased) {
      return (
        <div>
          <AntiBot captchaStatus={captchaStatus} update={update} />
          <hr className="spacer-md" />
        </div>
      );
    }
    return (<div />);
  }

  render() {
    const {
      form,
      embeddable,
      update,
      landingPageActions,
      feedbackEmail,
    } = this.props;
    const {
      feedbackAlertEnabled,
      feedbackAlert,
      notifyConversionEmailEnabled,
      notifyConversionEmail,
      downloadURL,
      redirectURL,
      formAction,
    } = landingPageActions;

    const {
      getFieldProps,
      getFieldError,
      setFieldsValue,
    } = form;
    return (
      <form>
        <EmbeddableTitle embeddable={embeddable} />
        <hr className="spacer-md" />
        <FormActions
          getFieldProps={getFieldProps}
          getFieldError={getFieldError}
          setFieldsValue={setFieldsValue}
          downloadURL={downloadURL}
          redirectURL={redirectURL}
          formAction={formAction}
          update={update}
        />
        <hr className="spacer-md" />
        { this.renderAntiBot() }
        <FeedbackMessage
          getFieldProps={getFieldProps}
          getFieldError={getFieldError}
          feedbackAlertEnabled={feedbackAlertEnabled}
          feedbackAlert={feedbackAlert}
          update={update}
        />
        <FeedbackEmail
          getFieldProps={getFieldProps}
          getFieldError={getFieldError}
          form={form}
          update={update}
          {...feedbackEmail}
        />
        { this.renderDoubleOptIn() }
        <hr className="spacer-md" />
        <EmailNotification
          getFieldProps={getFieldProps}
          getFieldError={getFieldError}
          notifyConversionEmailEnabled={notifyConversionEmailEnabled}
          notifyConversionEmail={notifyConversionEmail}
          update={update}
        />
      </form>
    );
  }
}

Form.propTypes = {
  form: formShape.isRequired,
  embeddable: embeddableType.isRequired,
  landingPageActions: PropTypes.instanceOf(Object),
  doubleOptIn: PropTypes.instanceOf(Object),
  feedbackEmail: PropTypes.instanceOf(Object),
  captchaStatus: PropTypes.bool,
  isGoogleRecaptchaReleased: PropTypes.bool,
  update: PropTypes.func,
};

Form.defaultProps = {
  landingPageActions: {},
  doubleOptIn: {},
  feedbackEmail: {},
  captchaStatus: false,
  isGoogleRecaptchaReleased: false,
  update: () => {},
};

const mapFlagsToProps = {
  isGoogleRecaptchaReleased: ['form_bases', 'google_recaptcha'],
};

export const BaseForm = createForm()(Form);
export default withFeatureFlagsMapping(mapFlagsToProps)(BaseForm);
