import React from 'react';
import PropTypes from 'prop-types';
import cs from 'classnames';


const IMAGE_PLACEHOLDER = '/assets/forms-thumbnails/generic-thumb.png';

const getImageURL = (image) => {
  if (image.url && window.locale) {
    return `/assets/forms-thumbnails/${image.url}.${window.locale}.png`;
  }
  return IMAGE_PLACEHOLDER;
};

class TemplateCard extends React.Component {
  constructor() {
    super();
    this.state = { open: false };
    this.handleKeyPress = this.handleKeyPress.bind(this);
  }

  handleKeyPress(open) { this.setState({ open }); }


  render() {
    const {
      title, image, description, labels, actions, style,
    } = this.props;
    const { open } = this.state;
    return (
      <div
        role="link"
        tabIndex="0"
        onKeyPress={() => this.handleKeyPress(!open)}
        className={cs('template-card thumbnail', {
          a11y: open,
        })}
        style={style}
      >
        <div className="template-overlay">
          <div className="template-info">
            <span className="template-info-name">{title}</span>
            <span className="template-info-author">{description}</span>
          </div>
          <div className="template-actions">{actions}</div>
        </div>
        <div className="template-image">
          <img src={getImageURL(image)} alt={image.alt} />
        </div>

        <div className="template-info">
          <span className="template-info-name">{title}</span>
          <span className="template-info-author">{description}</span>
        </div>

        <div className="template-labels">{labels}</div>
      </div>
    );
  }
}
TemplateCard.propTypes = {
  title: PropTypes.node.isRequired,
  image: PropTypes.shape({
    url: PropTypes.string,
    alt: PropTypes.string,
  }).isRequired,
  description: PropTypes.node,
  labels: PropTypes.node,
  actions: PropTypes.node,
  // eslint-disable-next-line react/forbid-prop-types
  style: PropTypes.object,
};

TemplateCard.defaultProps = {
  description: null,
  labels: null,
  actions: null,
  style: {},
};

export default TemplateCard;
