import React from 'react';
import PropTypes from 'prop-types';
import cs from 'classnames';
import I18n from '../../../../shared/i18n';

const t = (key, params) => I18n.t(`lead_gen.embeddable_forms.conversion_tab.redirect_url.${key}`, params);

const RedirectFormActionInput = (
  {
    isActive,
    redirectURL,
    inputName,
    inputId,
    update,
    getFieldProps,
    getFieldError,
  }
) => {
  return (
    <div
      className={cs('form-group', {
        'has-error': getFieldError('redirectURL'),
      })}
    >
      <input
        className="form-control"
        maxLength="255"
        size="255"
        type="text"
        name={inputName}
        id={inputId}
        disabled={!isActive}
        {...getFieldProps('redirectURL', {
          initialValue: redirectURL,
          onChange: e => update('landingPageActions', 'redirectURL', e.target.value),
          rules: [
            {
              type: 'url',
              required: true,
              message: t('error.url'),
            },
          ],
        })}
      />
      <small className="help-block">
        {getFieldError('redirectURL')}
      </small>
    </div>
  );
};

RedirectFormActionInput.propTypes = {
  isActive: PropTypes.bool,
  inputName: PropTypes.string.isRequired,
  inputId: PropTypes.string.isRequired,
  update: PropTypes.func,
  getFieldProps: PropTypes.func,
  getFieldError: PropTypes.func,
  redirectURL: PropTypes.string,
};

RedirectFormActionInput.defaultProps = {
  redirectURL: '',
  isActive: false,
  getFieldError: () => {},
  getFieldProps: () => {},
  update: () => {},
};

export default RedirectFormActionInput;
