import React from 'react';
import PropTypes from 'prop-types';
import {
  SelectBoxTab,
  SelectBox,
  SelectBoxContent,
  SelectBoxItem,
  SelectBoxPane,
} from '../select-boxes/index';

class FormActionSelect extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      currentActivePane: props.activePane,
    };

    this.setActiveItem = this.setActiveItem.bind(this);
  }

  setActiveItem(inputElement) {
    const { update } = this.props;
    this.setState({ currentActivePane: inputElement.value });
    update('landingPageActions', 'formAction', inputElement.value);
  }

  formActionItem(formActionConfig) {
    const { currentActivePane } = this.state;
    const { formActionInputName } = this.props;
    return (
      <SelectBoxItem
        title={formActionConfig.title}
        dataTarget={`#${formActionConfig.htmlID}`}
        imageUrl={formActionConfig.imageUrl}
        imageAlt={formActionConfig.title}
        value={formActionConfig.value}
        selected={currentActivePane === formActionConfig.value}
        type="radio"
        onClick={this.setActiveItem}
        key={formActionConfig.value}
        name={formActionInputName}
      />
    );
  }

  formActionPane(formActionConfig, inputProps, ActionInputComponent) {
    const {
      getFieldProps, getFieldError, setFieldsValue, update,
    } = this.props;
    const { currentActivePane } = this.state;
    return (
      <SelectBoxPane
        open
        htmlID={formActionConfig.htmlID}
        description={formActionConfig.description}
        key={formActionConfig.htmlID}
      >
        <ActionInputComponent
          getFieldProps={getFieldProps}
          getFieldError={getFieldError}
          setFieldsValue={setFieldsValue}
          inputName={formActionConfig.inputName}
          inputId={formActionConfig.inputId}
          isActive={currentActivePane === formActionConfig.value}
          update={update}
          {...inputProps}
        />
      </SelectBoxPane>
    );
  }

  renderActivePane() {
    const {
      redirectURL, downloadURL, configs,
    } = this.props;
    const { currentActivePane } = this.state;
    let inputProps = { };
    return configs.map((config) => {
      if (config.value === currentActivePane) {
        inputProps = { redirectURL, downloadURL };
        return this.formActionPane(
          config,
          inputProps,
          config.inputComponent
        );
      }
      return null;
    });
  }

  render() {
    const {
      configs,
    } = this.props;
    return (
      <SelectBoxTab>
        <SelectBox>
          {configs.map(config => this.formActionItem(config))}
        </SelectBox>
        <SelectBoxContent>
          {this.renderActivePane()}
        </SelectBoxContent>
      </SelectBoxTab>
    );
  }
}

FormActionSelect.propTypes = {
  activePane: PropTypes.string,
  redirectURL: PropTypes.string,
  downloadURL: PropTypes.string,
  getFieldProps: PropTypes.func,
  getFieldError: PropTypes.func,
  setFieldsValue: PropTypes.func,
  update: PropTypes.func,
  formActionInputName: PropTypes.string.isRequired,
  configs: PropTypes.instanceOf(Array).isRequired,
};

FormActionSelect.defaultProps = {
  activePane: '',
  redirectURL: '',
  downloadURL: '',
  getFieldError: () => {},
  getFieldProps: () => {},
  setFieldsValue: () => {},
  update: () => {},
};

export default FormActionSelect;
