import React, { Component } from 'react';
import PropTypes from 'prop-types';
import cs from 'classnames';
import I18n from '../../../shared/i18n';

const t = (key, params) => I18n.t(`lead_gen.form_bases.settings.antibot.${key}`, params);

class AntiBot extends Component {
  render() {
    const { captchaStatus, update } = this.props;

    return (
      <div id="antibot-row" className="row">
        <div className="col-sm-3">
          <div>
            <h5>
              {t('title')}
            </h5>
          </div>
        </div>
        <div className="col-sm-8">
          <div
            className={cs('checkbox')}
          >
            <label htmlFor="captcha-status-enabled">
              <input type="checkbox" id="captcha-status-enabled" onChange={e => update('captchaStatus', '', e.target.checked)} checked={captchaStatus} />
              {t('feedback.title')}
              <small className="help-block">{t('feedback.description')}</small>
            </label>
          </div>
        </div>
      </div>
    );
  }
}

AntiBot.propTypes = {
  captchaStatus: PropTypes.bool,
  update: PropTypes.func,
};

AntiBot.defaultProps = {
  captchaStatus: false,
  update: () => {},
};

export default AntiBot;
